
.recordOrderMod .ListItem {
  box-sizing: border-box;
  width: calc(100% - 1rem);
  margin: 1rem 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #fff;
  border-radius: 0.33333rem;
  overflow: hidden;
}
.recordOrderMod .ListItem .itemContent .row {
  height: 2rem;
  line-height: 2rem;
  overflow: hidden;
}
.recordOrderMod .ListItem .itemContent .row .site {
  display: inherit;
  width: calc(100% - 5rem);
  overflow: hidden;
  text-overflow: ellipsis;
}
.recordOrderMod .ListItem .itemContent .row1 {
  margin-bottom: 0.5rem;
}
.recordOrderMod .ListItem .itemContent .row1 .itemTime {
  float: left;
  display: inline-block;
  font-size: 1rem;
  color: #333;
}
.recordOrderMod .ListItem .itemContent .row1 .itemStatus {
  float: right;
  display: inline-block;
}
.recordOrderMod .ListItem .itemContent .row1 .itemStatus .text {
  float: right;
  color: #333;
  margin-right: 0.5rem;
}
.recordOrderMod .ListItem .itemContent .row1 .itemStatus .textRed {
  color: #f06553;
}
.recordOrderMod .ListItem .itemContent .row1 .itemStatus .textAsh {
  color: #999999;
}
.recordOrderMod .ListItem .itemContent .row1 i {
  font-size: 0.8333rem;
  color: #999999;
  float: right;
}
.recordOrderMod .ListItem .itemContent .row2,
.recordOrderMod .ListItem .itemContent .row3 {
  color: #666666;
  font-size: 0.83333rem;
}
.recordOrderMod .ListItem .itemContent .row2 i,
.recordOrderMod .ListItem .itemContent .row3 i {
  line-height: 1.8rem;
  font-size: 0.66666rem;
  float: left;
  margin-right: 0.3333rem;
}
.recordOrderMod .ListItem .itemContent .row2 i {
  color: #3cafb9;
}
.recordOrderMod .ListItem .itemContent .row3 i {
  color: #f06553;
}



