
.waiter {
  box-sizing: border-box;
  width: 100%;
  padding: 2rem 2.08333rem;
  background-color: #fff;
  position: relative;
  /*display: flex;*/
  /*align-items: center;*/
  /*justify-content: flex-start;*/
}
.waiter .left {
  float: left;
  padding-top: 0.3rem;
}
.waiter .left .TitleImg {
  height: 5rem;
  width: 5rem;
  border-radius: 50% !important;
  position: relative;
  overflow: hidden;
  margin-right: 1rem;
}
.waiter .left img {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
.waiter .right {
  float: left;
  max-width: 60%;
  overflow: hidden;
}
.waiter .right .row {
  font-size: 1rem;
}
.waiter .right .row1 {
  color: #333333;
  font-size: 1.2rem;
}
.waiter .right .row2 {
  font-size: 1rem;
  color: #666666;
}
.waiter .right .row2 .carName {
  width: 100%;
  margin-top: 0.3rem;
}
.waiter .right .row3 {
  color: #999999;
  margin-top: 0.4rem;
}
.waiter .right .row3 .carNo {
  border-radius: 1rem;
  color: rgba(255, 105, 87, 0.8);
}
.waiter .right .row3 .waiterCarType {
  padding: 0 0.4rem;
  color: #999;
  border-radius: 1rem;
}
.waiter .right .row3 .paotuiType {
  margin-top: 0.3rem;
  padding: 0 0.5rem;
  background-color: #f06553;
  color: #fff;
  line-height: 1.8rem;
  border-radius: 0.9rem;
}
.waiter .right .carName {
  display: inline-block;
  margin-right: 5px;
}
.waiter .waiterPhone {
  position: absolute;
  right: 1.5rem;
  top: 50%;
  width: 3rem;
  height: 3rem;
  transform: translate(0, -50%);
}
.waiter .waiterPhone a {
  width: 100%;
  height: 100%;
}
.waiter .waiterPhone i {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 2.5rem;
  color: #f06553;
}



