
.inputBox {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -100;
  background-color: rgba(0, 0, 0, 0.5);
}
.inputBox .inputBoxCont {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0, 0);
  opacity: 0;
  min-width: 70%;
  max-width: 80%;
  background-color: #fff;
  border-radius: 0.333rem;
  transition: 0.3s all;
}
.inputBox .inputBoxCont .title {
  font-size: 1.5rem;
  line-height: 2.5rem;
  margin-top: 1rem;
  color: #333;
  text-align: center;
}
.inputBox .inputBoxCont .inputBoxContent {
  padding: 1rem;
}
.inputBox .inputBoxCont .inputBoxBottom {
  text-align: center;
}
.inputBox .inputBoxCont .inputBoxBottom .but {
  float: left;
  display: inline-block;
  width: 50%;
  background-color: #eeeeee;
  color: #bbbbbb;
  line-height: 3.5rem;
  font-size: 1.25rem;
}
.inputBox .inputBoxCont .inputBoxBottom .affirmBut {
  border-radius: 0 0 0 0.333rem;
}
.inputBox .inputBoxCont .inputBoxBottom .cancelBut {
  background-color: #eeeeee;
  color: #bbbbbb;
  border-radius: 0 0 0.333rem 0;
}
.inputBox .inputBoxCont .inputBoxBottom .affirmButAc {
  background-color: #f06550;
  color: #fff;
}
.inputBox .othersPhoneInput {
  width: 100%;
  border: none;
  border-bottom: 1px solid #eee;
  font-size: 1.25rem;
  line-height: 2.5rem;
  text-align: center;
  margin-bottom: 1rem;
}
.inputBoxShow {
  z-index: 555;
}
.inputBoxShow .inputBoxCont {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1, 1);
}



