
.upAvatar {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 300;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.8);
}
.upAvatar .inputImg {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: -100;
}
.upAvatar .content {
  width: 70%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 50;
}
.upAvatar .content .img {
  box-sizing: border-box;
  width: 100%;
  border: 0.2rem solid #fff;
  position: relative;
}
.upAvatar .content .img img {
  width: 100%;
}
.upAvatar .content .replaceBut {
  background-color: #f06553;
  color: #fff;
  display: inline-block;
  border-radius: 0.333rem;
  padding: 1rem;
  margin-top: 1.5rem;
  position: relative;
  z-index: 10;
}
.upAvatar .modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}



