
.invoiceApply {
  box-sizing: border-box;
  width: 100%;
  padding-bottom: 1rem;
}
.invoiceApply .row {
  background: #fff;
}
.invoiceApply .row .row-title {
  line-height: 3.6rem;
  background: #f7f7f7;
  padding: 0 1rem;
  border: 1px solid #eee;
  border-left: none;
  border-right: none;
}
.invoiceApply .row .row-val {
  margin: 0 1rem;
}
.invoiceApply .row .val-col + .val-col {
  border-top: 1px solid #eee;
}
.invoiceApply .row .col-label {
  line-height: 3.6rem;
  display: inline-block;
  width: 6.6rem;
  font-size: 1.1rem;
  color: #666666;
}
.invoiceApply .row .col-val {
  display: inline-block;
  line-height: 3.6rem;
  font-size: 1.1rem;
  width: calc(100% - 7rem);
}
.invoiceApply .row .col-val input {
  line-height: 2rem;
  width: 100%;
  box-sizing: border-box;
  padding: 0 .5rem;
  border: none;
  font-size: 1.1rem;
  color: #232323;
}
.invoiceApply .row .col-val .radio-but {
  display: inline-block;
  width: 45%;
  box-sizing: border-box;
  border: 1px solid #eee;
  height: 3rem;
  margin: 1.5rem 0;
  line-height: 3rem;
  border-radius: .3rem;
  transition: .3s all;
}
.invoiceApply .row .col-val .radio-but + .radio-but {
  margin-left: calc(100% - 92%);
}
.invoiceApply .row .col-val .radio-but[ac=true] {
  border-color: #f06553;
  color: #f06553;
}
.invoiceApply .row .radio-round-but {
  display: inline-block;
  width: 1.3rem;
  line-height: 1.3rem;
  height: 1.3rem;
  vertical-align: middle;
  border-radius: 50%;
  text-align: center;
  border: 1px solid #eee;
  margin-right: .2rem;
  transition: .3s all;
}
.invoiceApply .row .radio-round-but .icon-chenggong {
  font-size: .4rem;
  opacity: 0;
  color: #fff;
  transition: .3s all;
}
.invoiceApply .row .radio-round-but[ac=true] {
  background: #f06553;
  border-color: #f06553;
}
.invoiceApply .row .radio-round-but[ac=true] .icon-chenggong {
  color: #fff;
  opacity: 1;
  display: inline;
}
.invoiceApply .row5 .iconfont {
  font-size: 1.8rem;
  display: inline-block;
  vertical-align: middle;
  margin-right: .3rem;
}
.invoiceApply .row5 .radio-round-but .iconfont {
  margin: 0;
}
.invoiceApply .but {
  margin: 1.5rem 1rem;
  line-height: 3.5rem;
  text-align: center;
  color: #fff;
  background: #f06553;
  border-radius: .3rem;
  font-size: 1.1rem;
}



