
@import 'assets/fonts/iconfont.css';
* {
  padding: 0;
  margin: 0;
  font-weight: normal;
  -webkit-overflow-scrolling: touch;
  -webkit-tap-highlight-color: rgba(240, 240, 240, 0);
}
::-webkit-scrollbar {
  background: red;
}
::-webkit-scrollbar {
  display: none !important;
  /*隐藏滚轮*/
  opacity: 0 !important;
}
::-webkit-scrollbar-track {
  opacity: 0 !important;
}
::-webkit-scrollbar-thumb {
  opacity: 0 !important;
}
html {
  /*font-size: 62.5%;*/
  font-size: 75%;
  min-height: 100%;
}
.picker {
  z-index: 300;
}
@media screen and (min-width: 125px) {
html {
    font-size: 80%;
}
}
@media screen and (min-width: 375px) {
html {
    font-size: 80%;
}
}
body {
  width: 100%;
  background-color: #f3f4f5;
  min-height: 100%;
}
ul,
li,
ol {
  list-style: none;
}
a {
  display: inline-block;
  text-decoration: none;
}
span {
  display: inline-block;
}
input,
textarea {
  outline: none;
}
.router-link-active,
.router-link-exact-active {
  background-color: rgba(0, 0, 0, 0);
}
.clear:after {
  content: "200B";
  height: 0;
  clear: both;
  font-size: 0;
  display: block;
  *zoom: 1;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100vw;
  height: 100vh;
  background: #f3f4f5;
}
.MidContent {
  box-sizing: border-box;
  width: 100%;
}
/** 弹窗**/
.modelBox .Box {
  position: fixed;
  min-width: 46.666666%;
  max-width: 75%;
  height: auto;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 0.33333rem;
  overflow: hidden;
  z-index: -100;
  opacity: 0;
  transition: 0.3s all;
  text-align: center;
}
.modelBox .Box .modelBoxContent {
  padding: 1rem;
}
.modelBox .Box .modelBoxBottom {
  height: 2.8rem;
  width: 100%;
  overflow: hidden;
}
.modelBox .Box .modelBoxBottom .ActionBut {
  width: 50%;
  box-sizing: border-box;
  text-align: center;
  float: left;
  line-height: 2.8rem;
}
.modelBox .Box .modelBoxBottom .OKBut {
  background-color: #eeeeee;
  color: #bbbbbb;
  border-radius: 0 0 0 0.33333rem;
}
.modelBox .Box .modelBoxBottom .NoBut {
  background-color: #f06553;
  color: #fff;
  border-radius: 0 0 0.33333rem 0;
}
.modelBox .modelBoxModel {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  z-index: -100;
  transition: 0.3s all;
}
.modelRgba {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: #fff;
}



