
.upImgDom {
  height: 3.1666rem;
  text-align: left;
}
.upImgDom .showImgList {
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}
.upImgDom .showImgList .showImgItem {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
  height: 3.1666rem;
  width: 3.1666rem;
  position: relative;
  border: 1px solid #eee;
  background-color: #fff;
  margin: 0 0.25rem;
}
.upImgDom .showImgList .showImgItem img {
  height: 90%;
  width: 90%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.upImgDom .AddImg {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
  height: 3.1666rem;
  width: 3.1666rem;
  border: 1px dashed #eee;
  position: relative;
  background-color: #fff;
}
.upImgDom .AddImg b {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.5rem;
  color: #999;
  font-weight: bold;
}
.upImgDom input {
  position: absolute;
  z-index: -100;
  opacity: 0;
  left: 0;
  top: 0;
}
.upImgDom .showMaxImg {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.upImgDom .showMaxImg img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  z-index: 50;
  border: 1rem solid #fff;
}
.upImgDom .showMaxImg .modal {
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.upImgDom .showMaxImgAction {
  z-index: 100;
}



