
.rollBanner {
  width: 100%;
  height: 100%;
  border-radius: 0.333rem;
  overflow: hidden;
  position: relative;
  background-color: #fff;
}
.rollBanner .imgList {
  width: 100%;
  height: 100%;
  position: relative;
}
.rollBanner .imgList li {
  width: 100%;
  height: 100%;
  position: absolute;
  transition: 0.8s all;
  z-index: 0;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}
.rollBanner .imgList li .img {
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: 0.3s all;
}
.rollBanner .imgList li .img a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.rollBanner .imgList li .img img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}
.rollBanner .imgList .liAc {
  z-index: 20;
  opacity: 1;
}
.rollBanner .imgList .liAc .img {
  opacity: 1;
}
.rollBanner .Buttons {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 0);
  z-index: 50;
}
.rollBanner .Buttons li {
  width: 1.8rem;
  height: 1rem;
  position: relative;
  float: left;
}
.rollBanner .Buttons li .but {
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 0.5rem;
  height: 0.5rem;
  margin: 0 0.666rem;
  border: 1px solid #f06553;
  opacity: 0.8;
  border-radius: 0.333rem;
  transition: 0.6s all;
  transform-origin: 50% 50%;
  transform: scale(1, 1) translate(-50%, -50%);
}
.rollBanner .Buttons li .butAc {
  background-color: #f06553;
  border-color: #f06553;
  transform: scale(1.2, 1.2) translate(-50%, -50%);
}



