
.carTypeScroll {
  width: 100%;
  height: 10rem;
}
.carTypeScroll .Content {
  width: 100%;
  height: 100%;
  position: relative;
}
.carTypeScroll .Content .arrows {
  position: absolute;
  z-index: 10;
}
.carTypeScroll .Content .arrows i {
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.5);
}
.carTypeScroll .Content .arrowsLeft {
  left: 0;
  top: 50%;
  transform: translate(0, -50%) rotate(180deg);
}
.carTypeScroll .Content .arrowsRight {
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.carTypeScroll .Content .ScrollCont {
  position: relative;
  width: 100%;
  height: inherit;
}
.carTypeScroll .Content .ScrollCont ul {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.carTypeScroll .Content .ScrollCont ul li {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s all;
  opacity: 0;
}
.carTypeScroll .Content .ScrollCont ul li img {
  width: 100%;
}
.carTypeScroll .Content .ScrollCont ul .ScrollContLiShow {
  opacity: 1;
}



