
.siteInput {
  width: 100%;
  height: 100%;
  position: relative;
}
.siteInput .siteInputShow {
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  border: none;
  width: 100%;
  font-size: 1.1666rem;
  height: 3.1666rem;
  z-index: 5;
  overflow: hidden;
  text-overflow: ellipsis;
}
.siteInput .placeholder {
  width: 100%;
  color: #999;
}
.siteInput .without {
  text-align: center;
  margin-top: 2rem;
}
.siteInput .result {
  position: fixed;
  height: 100%;
  top: 150%;
  left: 0;
  background-color: #f3f4f5;
  width: 100%;
  border-radius: 0.333rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  z-index: -100;
  opacity: 1;
  overflow: hidden;
  overflow-y: auto;
  line-height: 1.5rem !important;
  transition: 0.3s all;
}
.siteInput .result .Title {
  display: inline-block;
  height: 3.5rem;
  line-height: 3.5rem;
  width: 100%;
  max-width: none;
  position: relative;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background: #fff;
  z-index: 80;
}
.siteInput .result .Title .selectCity {
  float: left;
  box-sizing: border-box;
  padding-right: 0.5rem;
  width: 6.5rem;
  height: inherit;
  line-height: 3.5;
  text-align: center;
  position: relative;
}
.siteInput .result .Title .selectCity .city {
  box-sizing: border-box;
  display: inline-block;
  width: 5rem;
  margin-right: 0.5rem;
  height: inherit;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1rem;
}
.siteInput .result .Title .selectCity .icon {
  position: absolute;
  right: 0.5rem;
  top: 50%;
  width: 1.2rem;
  height: 1.2rem;
  transform: translate(0, -50%);
}
.siteInput .result .Title .selectCity .icon i {
  font-size: 0.8rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transition: 0.3s all;
  transform: translate(-50%, -50%) rotate(90deg);
  color: #666;
  margin: 0;
}
.siteInput .result .Title .selectCity::after {
  content: '';
  width: 1px;
  height: 50%;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  background: #eee;
}
.siteInput .result .Title .selectCityShow .icon i {
  transform: translate(-50%, -50%) rotate(-90deg);
}
.siteInput .result .Title input {
  margin-top: 0.2rem;
  box-sizing: border-box;
  display: inline-block;
  width: calc(100% - 10rem) !important;
  border: none;
  font-size: 1.1666rem;
  position: relative;
  z-index: 5;
  padding: 0.5rem;
}
.siteInput .result .Title .closeBut {
  position: absolute;
  right: 0.5rem;
  top: 0;
  z-index: 50;
  color: #666;
}
.siteInput .result .resultCont {
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem 0 1rem;
  margin-top: 1rem;
  overflow: hidden;
  overflow-y: scroll;
  height: calc(100% - 3.5rem);
}
.siteInput .result .resultCont ul {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 0.333rem;
}
.siteInput .result .resultCont p {
  color: #bbb;
}
.siteInput .result .resultCont .without {
  width: 100%;
}
.siteInput .result .resultCont li {
  padding: 0.3rem 1rem;
  border-bottom: 1px solid #eee;
  background: #fff;
}
.siteInput .result .resultCont li h3 {
  font-weight: normal;
  margin-bottom: 0.4rem;
  margin-top: 0.5rem;
  font-size: 1.2rem;
}
.siteInput .result .resultCont li p {
  margin: 0.4rem 0;
}
.siteInput .result .resultCont li:nth-last-child(1) {
  border-bottom: none;
}
.siteInput .showResult {
  z-index: 600;
  opacity: 1;
  top: 0;
}
.siteInput .module {
  display: block;
  position: fixed;
  left: 50%;
  top: 50%;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  transform: translate(-50%, -50%);
  opacity: 1;
  z-index: -100;
}
.siteInput .moduleShow {
  opacity: 0;
  z-index: 50;
}
.siteInput .cityMod {
  width: 100%;
  height: calc(100vh - 3.5rem);
  overflow-y: scroll;
  position: relative;
  z-index: 20;
}
.siteInput .cityMod .cityList .cityRow,
.siteInput .cityMod .hotCity .cityRow {
  width: 100%;
  box-sizing: border-box;
  padding: 0.5rem 1rem;
}
.siteInput .cityMod .cityList .cityRow .title,
.siteInput .cityMod .hotCity .cityRow .title {
  box-sizing: border-box;
  text-align: left;
  width: 100%;
  padding: 0 1rem;
  max-width: none !important;
  font-size: 1.2rem;
  border-radius: 0.333rem 0.333rem 0 0;
  background: #fff;
}
.siteInput .cityMod .cityList .cityRow .title i,
.siteInput .cityMod .hotCity .cityRow .title i {
  color: #f06553;
}
.siteInput .cityMod .cityList .cityRow .content,
.siteInput .cityMod .hotCity .cityRow .content {
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem;
  text-align: left;
  background: #fff;
  border-radius: 0 0 0.333rem 0.333rem;
}
.siteInput .cityMod .cityList .cityRow .content ul li,
.siteInput .cityMod .hotCity .cityRow .content ul li {
  width: 100%;
  box-sizing: border-box;
  padding: 0 0.5rem;
  line-height: 3rem;
  border-bottom: 1px solid #eee;
  color: #666;
}
.siteInput .cityMod .cityList .cityRow .content ul li:nth-last-child(1),
.siteInput .cityMod .hotCity .cityRow .content ul li:nth-last-child(1) {
  border-bottom: none;
}
.siteInput .cityMod .hotCity {
  padding-top: 0.5rem;
}
.siteInput .cityMod .hotCity .Left {
  float: left;
  font-size: 1rem;
}
.siteInput .cityMod .hotCity .Left i {
  line-height: 2.3;
  color: #f06553;
}
.siteInput .cityMod .hotCity .Right {
  float: right;
  font-size: 0.833rem;
}
.siteInput .cityMod .hotCity .Right i {
  line-height: 2;
  color: #666;
}
.siteInput .replaceCityList {
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  width: 2rem;
  z-index: 30;
}
.siteInput .replaceCityList ul {
  position: absolute;
  top: 50%;
  right: 0;
  width: inherit;
  transform: translate(0, -50%);
  background: rgba(0, 0, 0, 0.1);
  border-radius: 0.333rem 0 0 0.333rem;
  overflow: hidden;
}
.siteInput .replaceCityList ul li {
  box-sizing: border-box;
  width: inherit;
  text-align: center;
  line-height: 1.6rem;
  height: 1.6rem;
  float: none;
  transition: 0.3s all;
  position: relative;
}
.siteInput .replaceCityList ul li span {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: rgba(0, 0, 0, 0.2);
  font-weight: 400;
  transition: 0.3s all;
}
.siteInput .replaceCityList ul .ac {
  background: #f06553 !important;
}
.siteInput .replaceCityList ul .ac span {
  color: #fff !important;
}
.siteInput .shortcut {
  background: #fff;
}
.siteInput .shortcut .shortcut-row {
  float: left;
  box-sizing: border-box;
  line-height: 3.6rem;
  height: 3.2rem;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
  width: 20%;
  position: relative;
}
.siteInput .shortcut .shortcut-row + .shortcut-row::after {
  position: absolute;
  content: '';
  height: 1rem;
  width: 1px;
  background: #eee;
  display: inline-block;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.siteInput .shortcut .shortcut-row-ac {
  color: #f06553;
}
@media screen and (max-width: 375px) {
html .siteInput .replaceCityList ul {
    top: 55%;
}
}



