
.downOrder {
  width: 100%;
}
.downOrder .content {
  width: 100%;
  padding-bottom: 1rem;
}
.downOrder .content .labels {
  box-sizing: border-box;
  width: 100%;
  background-color: #fff;
  padding: 0.5rem;
  height: 13rem;
  overflow: hidden;
  overflow-y: auto;
}
.downOrder .content .labels .list {
  box-sizing: border-box;
  width: 25%;
  float: left;
  position: relative;
  height: 6.25rem;
  transition: 0.3s all;
  border: 1px solid rgba(0, 0, 0, 0);
}
.downOrder .content .labels .list .listContent {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.downOrder .content .labels .list .listContent p {
  font-size: 0.8333rem;
  color: #5a5a5a;
}
.downOrder .content .labels .list .listContent .icon {
  width: 2.16rem;
}
.downOrder .content .labels .list .listContent .icon img {
  width: 100%;
}
.downOrder .content .labels .listAc p {
  color: #f06553 !important;
}
.downOrder .content .items {
  width: 100%;
  box-sizing: border-box;
  margin-top: 1rem;
  background-color: #fff;
  padding: 0 1.25rem;
  padding-left: 3.166rem;
}
.downOrder .content .items .row {
  line-height: 3.1666rem;
  border-bottom: 1px solid #eee;
  width: 100%;
  position: relative;
}
.downOrder .content .items .row .title {
  display: inline-block;
  height: 100%;
  max-width: 5.1rem;
  margin-right: 0.5rem;
  float: left;
}
.downOrder .content .items .row .title img {
  display: inline-block;
  width: 1.25rem;
  vertical-align: middle;
  margin-right: 0.833rem;
  position: absolute;
  left: -2rem;
  top: 50%;
  transform: translate(0, -50%);
}
.downOrder .content .items .row .title p {
  width: 100%;
  float: left;
  display: inline-block;
  font-size: 1rem;
  color: #a5a5a5;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.downOrder .content .items .row .val {
  width: calc(100% - 11rem);
  font-size: 1rem;
  float: left;
}
.downOrder .content .items .row .val input {
  font-size: 1rem !important;
}
.downOrder .content .items .row .detai {
  box-sizing: border-box;
  width: 7.5rem;
  text-align: center;
  float: right;
  position: relative;
  z-index: 5;
}
.downOrder .content .items .row .detai:after {
  content: '';
  display: inline-block;
  width: 1px;
  height: 50%;
  position: absolute;
  background-color: #eee;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.downOrder .content .items .detailAdd {
  height: 100%;
}
.downOrder .content .items .detailAdd .val {
  width: calc(100% - 6rem );
  height: 100%;
}
.downOrder .content .items .detailAdd .val input {
  display: inline-block;
  width: 100%;
  line-height: 1.8rem;
  border: none;
  font-size: 1.2rem !important;
}
.downOrder .content .remark .row {
  border: none;
}
.downOrder .content .remark .val {
  margin-top: -1rem;
  height: 6rem;
  width: 100%;
}
.downOrder .content .remark .val textarea {
  border: none;
  width: 100%;
  height: 100%;
  font-size: 1.2rem;
}
.downOrder .content .add .row:nth-child(1) {
  margin: 0;
}
.downOrder .content .add .row {
  margin-top: -3px;
}
.downOrder .content .add .row:nth-last-child(1) {
  border-bottom: none;
}
.downOrder .content .phones .row {
  margin-top: -3px;
}
.downOrder .content .phones .row .val {
  width: calc(100% - 10rem);
}
.downOrder .content .phones .row .val input {
  width: 100%;
  font-size: 1.2rem;
  border: none;
}
.downOrder .content .phones .row:nth-last-child(1) {
  border: none;
}
.downOrder .content .phones .row:nth-child(1) {
  margin: 0;
}
.downOrder .content .page1 .row {
  margin-top: -3px;
}
.downOrder .content .page1 .row .val {
  width: calc(100% - 10rem);
}
.downOrder .content .page1 .row .val input {
  width: 100%;
  font-size: 1.2rem;
  border: none;
}
.downOrder .content .page1 .row:nth-last-child(1) {
  border: none;
}
.downOrder .content .page1 .row:nth-child(1) {
  margin: 0;
}
.downOrder .content .page2 .row {
  margin-top: -3px;
}
.downOrder .content .page2 .row .val {
  width: calc(100% - 13rem);
}
.downOrder .content .page2 .row .val input {
  width: 100%;
  font-size: 1.2rem;
  border: none;
}
.downOrder .content .page2 .row:nth-last-child(1) {
  border: none;
}
.downOrder .content .page2 .row:nth-child(1) {
  margin: 0;
}
.downOrder .content .page2 .price .val {
  width: calc(100% - 13rem);
}
.downOrder .content .page2 .price .checkInput {
  padding-left: 0.6rem;
}
.downOrder .content .Sum {
  text-align: center;
  margin-top: 2rem;
}
.downOrder .content .Sum i {
  float: none;
  color: #999;
  font-size: 0.8333rem;
}
.downOrder .content .Sum p {
  color: #3f3f3f;
  font-size: 0.833rem;
}
.downOrder .content .Sum .sumNum {
  font-size: 2rem;
  color: #f06553;
}
.downOrder .content .Sum .detailSum {
  margin-left: 0.5rem;
}
.downOrder .content .Sum .detailSum i {
  color: #8d8d8d;
}
.downOrder .content .Sum .couponSelect {
  margin-top: 0.3rem;
}
.downOrder .content .upImgShow {
  height: 100%;
  width: calc(100% - 5rem) !important;
}
.downOrder .content .confirmBut {
  padding: 0 1rem;
  margin-top: 2.5rem;
  margin-bottom: 3rem;
  position: relative;
  z-index: 1;
}
.downOrder .content .confirmBut span {
  width: 100%;
  text-align: center;
  background-color: #f06553;
  color: #fff;
  font-size: 1.4rem;
  line-height: 3.666rem;
  height: 3.666rem;
  border-radius: 0.333rem;
}
.downOrder .content .accessorialService .checkInput {
  float: left;
  margin: 0 0.4rem;
}
.downOrder .costBox ul li {
  height: 2rem;
  line-height: 2rem;
  color: #999999;
}
.downOrder .costBox ul li .name {
  float: left;
}
.downOrder .costBox ul li .time,
.downOrder .costBox ul li .val {
  float: right;
}
.downOrder .costBox ul li .time {
  margin-right: 0.8rem;
  color: #666;
}
.downOrder .costBox ul li .sum {
  color: #f06553;
}
.downOrder .costBox ul li .unit {
  margin-left: 0.3rem;
}
.downOrder .costBox .introduce {
  border-top: 1px solid #eee;
  margin-top: 0.5rem;
  padding-top: 1.2rem;
  line-height: 1.8rem;
  color: #666;
}



