
.score {
  display: inline-block;
  text-align: center;
  margin-top: 3.1666rem;
}
.score ul {
  display: inline-block;
}
.score ul li {
  float: left;
  margin: 0 0.625rem;
}
.score ul li span {
  width: 2rem;
  height: 2rem;
  transition: 0.3s all;
}
.score ul li .star {
  background: url(../../../../assets/img/pingjia/xing.png) no-repeat center;
  background-size: 100% 100%;
}
.score ul li .starAc {
  background: url(../../../../assets/img/pingjia/xingAc.png) no-repeat center;
  background-size: 100% 100%;
}
.score .text {
  font-size: 1rem;
  color: #f06553;
  margin-top: 0.823rem;
}
.score .text .textAsh {
  color: #999999;
}



