
.Add {
  background-color: #fff;
  padding: 1rem 2rem;
}
.Add .row {
  height: 2.5rem;
  line-height: 2.5rem;
  font-size: 1.25rem;
}
.Add .row i {
  margin-right: 0.5rem;
  float: left;
}
.Add .row .text {
  display: inherit;
  width: calc(100% - 2rem);
  overflow: hidden;
  text-overflow: ellipsis;
  height: 2rem;
}
.Add .row1 {
  color: #333;
  height: 2.6rem;
  line-height: 2.6rem;
}
.Add .row1 i {
  display: inline-block;
  height: 2.6rem;
  line-height: 2.6rem;
  font-size: 1rem;
  color: #fab10a;
}
.Add .row2,
.Add .row3 {
  font-size: 1.2rem;
  color: #999999;
}
.Add .row2 i,
.Add .row3 i {
  display: inline-block;
  height: 2rem;
  line-height: 2rem;
  font-size: 1rem;
}
.Add .row2 i {
  color: #3cafb9;
}
.Add .row3 i {
  color: #f06553;
}
.Add .waypoint .row {
  position: relative;
}
.Add .waypoint .row i {
  margin-left: -0.1rem;
  color: #999;
  position: relative;
  z-index: 5;
  transition: 0.3s all;
}
.Add .waypoint .row .text {
  color: #999999;
  margin-left: -0.2rem;
}
.Add .waypoint .row::after {
  content: '';
  background: #E8E8E8;
  height: 185%;
  width: 1px;
  position: absolute;
  left: 0.45rem;
  top: 50%;
  transform: translate(0, -50%);
  transition: 0.3s all;
}
.Add .waypoint .row:nth-last-child(1)::after {
  height: 135%;
}
.Add .waypoint .arrive i {
  color: #00c801;
}
.Add .waypoint .arrive::after {
  background: #1E2021;
}



