
.invoice {
  width: 100%;
  height: 100%;
}
.invoice .invoice-type {
  margin: 1rem;
  padding: 1rem;
  background: #fff;
  border-radius: .4rem;
}
.invoice .invoice-type .type-row {
  margin-bottom: 1.6rem;
}
.invoice .invoice-type .row-col {
  line-height: 2.2rem;
  padding: 0 1.2rem;
  color: #999;
  border-radius: 1.1rem;
  border: 1px solid rgba(0, 0, 0, 0.05);
  transition: 0.3s all;
}
.invoice .invoice-type .row-col + .row-col {
  margin-left: .8rem;
}
.invoice .invoice-type .row-col[ac=true] {
  color: #f06553;
  border-color: #f06553;
}
.invoice .type-but {
  line-height: 3.4rem;
  background: rgba(0, 0, 0, 0.1);
  text-align: center;
  border-radius: .4rem;
  color: #999;
  font-size: 1.1rem;
  transition: 0.3s all;
}
.invoice .type-but[ac=true] {
  background: #f06553;
  color: #fff;
}
.invoice .operating {
  margin: 1rem;
  border-radius: .4rem;
  padding: 0 1rem;
  background: #fff;
}
.invoice .opt-row {
  line-height: 3.8rem;
  font-size: 1.2rem;
  color: #333;
}
.invoice .iconfont {
  float: right;
  font-size: 1rem;
  color: #999;
}
.invoice .opt-row + .opt-row {
  border-top: 1px solid #f7f7f7;
}



