
.invoiceRecord {
  width: 100%;
  box-sizing: border-box;
  height: 100vh;
}
.invoiceRecord .loadDataBut {
  box-sizing: border-box;
  width: 100%;
  line-height: 3.5rem;
}
.invoiceRecord .loadDataBut span {
  width: 100%;
  background-color: #fff;
  border-radius: 0.333rem;
  text-align: center;
}
.invoiceRecord .invoiceRecordMod {
  margin: 1rem;
  width: calc(100% - 2rem);
}
.invoiceRecord .wujilv {
  height: 40vh;
  padding-top: 30vh;
}
.invoiceRecord .wujilv h2 {
  text-align: center;
}



