
.BoxSave {
  width: 52vw;
  position: relative;
  z-index: -100;
}
.BoxSave .Box {
  position: fixed;
  min-width: 46.666666%;
  max-width: 75%;
  height: auto;
  background-color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 0.33333rem;
  overflow: hidden;
  z-index: -100;
  opacity: 0;
  transition: 0.3s all;
  text-align: center;
}
.BoxSave .Box .modelBoxContent {
  padding: 1rem;
}
.BoxSave .Box .modelBoxBottom {
  height: 2.8rem;
  width: 100%;
  overflow: hidden;
}
.BoxSave .Box .modelBoxBottom .ActionBut {
  width: 50%;
  box-sizing: border-box;
  text-align: center;
  float: left;
  line-height: 2.8rem;
}
.BoxSave .Box .modelBoxBottom .OKBut {
  background-color: #eeeeee;
  color: #bbbbbb;
  border-radius: 0 0 0 0.33333rem;
}
.BoxSave .Box .modelBoxBottom .NoBut {
  background-color: #f06553;
  color: #fff;
  border-radius: 0 0 0.33333rem 0;
}
.BoxSave .Icon {
  display: inline-block;
  width: 4.1rem;
  height: 4.1rem;
  border-radius: 50%;
  background-color: #f3f4f5;
  overflow: hidden;
  position: relative;
}
.BoxSave .Icon i {
  font-size: 1.5rem;
  position: absolute;
  left: 52%;
  top: 52%;
  font-weight: bold;
  transform: translate(-50%, -50%);
}
.BoxSave .Icon .icon-chenggong {
  color: #2ba81e;
}
.BoxSave .Icon .icon-iconfontclosesmall {
  color: red;
  font-size: 1.8rem;
}
.BoxSave .text {
  color: #333333;
  font-size: 1.25rem;
  margin-top: 1.6666rem;
}
.BoxSave .model {
  position: fixed;
  width: 300%;
  height: 300%;
  background: rgba(0, 0, 0, 0.5);
  left: 0;
  top: 0;
  opacity: 1;
  z-index: -100;
  transform: translate(-50%, -50%);
}
.showModelBox {
  z-index: 777;
}
.showModelBox .Box {
  z-index: 50;
  opacity: 1;
}
.showModelBox .model {
  opacity: 1;
  z-index: 10;
}



