
.settings {
  width: 100vw;
  height: 100vh;
  background-color: #f3f4f5;
}
.settings .page {
  padding-top: 1rem;
}
.settings .page .pageItme {
  background-color: #fff;
}
.settings .page li {
  box-sizing: border-box;
  padding: 0 1rem;
  margin: 0 1rem;
  border-bottom: 1px solid #f3f4f5;
  height: 3.666666rem;
  line-height: 3.666666rem;
  font-size: 1rem;
}
.settings .page li a {
  color: #333333;
  width: 100%;
}
.settings .page li i {
  float: right;
  line-height: 3.666666rem;
  font-size: 1rem;
  color: #999999;
}
.settings .page li span {
  float: right;
  color: #999999;
}
.settings .page li:nth-last-child(1) {
  border-bottom: none;
}
.settings .logoutBut {
  padding: 0 1rem;
  margin-top: 1rem;
}
.settings .logoutBut .But,
.settings .logoutBut a {
  width: 100%;
  height: 3.666666rem;
  line-height: 3.666666rem;
  color: #fff;
  background-color: #f06553;
  border-radius: 0.5rem;
  text-align: center;
  font-size: 1rem;
}
.settings .modelBox .modelBoxContent {
  text-align: center;
}
.settings .modelBox .modelBoxContent .Icon {
  width: 4.08333rem;
  height: 4.08333rem;
  overflow: hidden;
  border-radius: 50%;
  background-color: #f3f4f5;
  position: relative;
}
.settings .modelBox .modelBoxContent .Icon i {
  position: absolute;
  font-size: 1.8rem;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #999999;
}
.settings .modelBox .modelBoxContent .text {
  font-size: 1rem;
  color: #333333;
  margin-top: 0.8rem;
}
.settings .showModelBox .Box {
  opacity: 1;
  z-index: 100;
}
.settings .showModelBox .Box .NoBut {
  border-radius: 0 0 0 0 !important;
}
.settings .showModelBox .Box .OKBut {
  border-radius: 0 0 0 0 !important;
}
.settings .showModelBox .modelBoxModel {
  z-index: 50;
  opacity: 1;
}



