
.invoiceRecordMod {
  box-sizing: border-box;
  width: 100%;
  padding: 1rem;
  background: #fff;
  margin-bottom: 1rem;
  border-radius: 0.333rem;
}
.invoiceRecordMod .page {
  line-height: 2.2rem;
}
.invoiceRecordMod .page .title {
  float: left;
  color: #333;
  margin-right: 0.5rem;
  width: 1rem;
  line-height: 1.7rem;
  height: 2.2rem;
  font-size: 1.8rem;
}
.invoiceRecordMod .page .title .icon-shijian1 {
  color: #c5c5c5;
  font-size: 1rem;
}
.invoiceRecordMod .page .val {
  color: #999;
  float: left;
  line-height: 2.2rem;
  height: 2.2rem;
  font-size: 1rem;
  width: calc(100% - 10rem);
}
.invoiceRecordMod .page .status {
  color: #999;
  width: 3.4rem;
  float: right;
  height: 2.2rem;
}
.invoiceRecordMod .page .status .icon-jiantou {
  font-size: 1rem;
  color: #9a9a9a;
}
.invoiceRecordMod .page .sum {
  float: right;
  height: 2.2rem;
  font-size: 2rem;
  color: #333;
}
.invoiceRecordMod .page .statusShow {
  color: #f06553;
}



