
.compileNom {
  background-color: #f3f4f5;
  width: 100vw;
  height: 100vh;
}
.compileNom .page {
  margin-bottom: 1rem;
  padding: 0 1rem;
  background-color: #fff;
}
.compileNom .page .pageItem {
  box-sizing: border-box;
  padding: 0 1rem;
  border-bottom: 1px solid #f3f4f5;
  height: 3.75rem;
  line-height: 3.75rem;
}
.compileNom .page .pageItem .pageItemTitle {
  width: 8rem;
  float: left;
  height: 100%;
}
.compileNom .page .pageItem .pageItemContent {
  width: calc(100% - 8rem);
  float: left;
}
.compileNom .page .pageItem .pageItemContent .genderSelect {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
  padding: 0 1rem;
  height: 2rem;
  line-height: 2rem;
  border-radius: 1rem;
  overflow: hidden;
  border: 1px solid #e1e1e1;
  color: #999999;
  transition: 0.3s all;
  background-color: #fff;
  font-size: 1rem;
}
.compileNom .page .pageItem .pageItemContent .genderSelect i {
  display: inline-block;
  margin-right: 0.9rem;
  font-size: 1rem;
  height: 2rem;
  color: #c5c4c4;
  transition: 0.3s all;
}
.compileNom .page .pageItem .pageItemContent .genderSelect:nth-child(1) {
  margin-right: 0.5rem;
}
.compileNom .page .pageItem .pageItemContent .genderSelect:nth-child(2) {
  margin-left: 0.5rem;
}
.compileNom .page .pageItem .pageItemContent .ShowGenderSelect {
  background-color: #f06553;
  border: 1px solid #f06553;
  color: #fff;
}
.compileNom .page .pageItem .pageItemContent .ShowGenderSelect i {
  color: #fff;
}
.compileNom .page .pageItem .pageItemContent input {
  display: inline-block;
  vertical-align: middle;
  line-height: 3rem;
  border: none;
  width: 100%;
  box-sizing: border-box;
}
.compileNom .page .pageItem:nth-last-child(1) {
  border-bottom: none;
}
.compileNom .page .headItme {
  height: 10rem;
  line-height: 10rem;
  color: #999999;
}
.compileNom .page .headItme .head {
  width: 100%;
  text-align: center;
}
.compileNom .page .headItme .head .headImg {
  display: inline-block;
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  border: 1px solid #eee;
  overflow: hidden;
  position: relative;
  vertical-align: middle;
}
.compileNom .page .headItme .head .headImg img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.compileNom .saveItem {
  padding: 0 1rem;
  height: 3.66666rem;
  line-height: 3.66666rem;
}
.compileNom .saveItem span {
  color: #fff;
  width: 100%;
  text-align: center;
  background-color: #f06553;
  border-radius: 0.3333rem;
  font-size: 1rem;
}
.compileNom .upAvatar {
  z-index: 300;
}



