
.carType {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -100;
}
.carType .carTypeCont {
  position: absolute;
  bottom: -100%;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 200;
  line-height: 3.75rem;
  text-align: center;
  transition: 0.3s all;
  opacity: 0;
}
.carType .carTypeCont .title {
  color: #999999;
  font-size: 1.25rem;
  border-bottom: 1px solid #eee;
}
.carType .carTypeCont .content {
  max-height: 15rem;
  overflow-y: scroll;
}
.carType .carTypeCont .content .row {
  box-sizing: border-box;
  padding: 0 1rem;
  width: 100%;
  height: 3.75rem;
  overflow: hidden;
  border-bottom: 1px solid #eee;
  position: relative;
}
.carType .carTypeCont .content .row .remove,
.carType .carTypeCont .content .row .push {
  position: relative;
  width: 2.5rem;
  height: 100%;
}
.carType .carTypeCont .content .row .remove i,
.carType .carTypeCont .content .row .push i {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #4c4c4c;
}
.carType .carTypeCont .content .row .remove {
  float: left;
}
.carType .carTypeCont .content .row .push {
  float: right;
}
.carType .carTypeCont .content .row .val {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #333333;
}
.carType .carTypeCont .content .row .val .num b {
  color: #f06553;
  font-size: 1.5rem;
  margin: 0 0.2rem;
}
.carType .carTypeCont .bottom {
  width: 100%;
}
.carType .carTypeCont .bottom .but {
  width: 50%;
  text-align: center;
  float: left;
  font-size: 1.25rem;
}
.carType .carTypeCont .bottom .reset {
  color: #bcbcbc;
  background-color: #eee;
}
.carType .carTypeCont .bottom .affirm {
  color: #fff;
  background-color: #f06553;
}
.carType .carTypeCont .bottom .resetShow {
  color: #f06553;
}
.carType .wucheliang h2 {
  text-align: center;
}
.carType .modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100;
}
.carTypeShow {
  z-index: 300;
  opacity: 1;
}
.carTypeShow .carTypeCont {
  bottom: 0;
  opacity: 1;
}



