
.typeTabs {
  width: 100%;
  height: 100%;
  position: relative;
  min-height: 3.2rem;
}
.typeTabs .TabsContent {
  width: calc(100% - 3.6rem);
  float: left;
  height: 100%;
  overflow-x: auto;
  z-index: 200;
}
.typeTabs .TabsContent ul {
  /*width: auto;*/
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.typeTabs .TabsContent ul li {
  height: 100%;
  float: left;
  padding: 0 1.6rem;
  transition: 0.3s all;
  line-height: 3.2;
  color: #666666;
  min-width: 6rem;
  text-align: center;
}
.typeTabs .TabsContent ul .AcLi {
  height: 100%;
  background-color: #fff;
  color: #f06553;
  text-align: center;
}
.typeTabs .menusBut {
  float: right;
  text-align: center;
  width: 3.6rem;
  height: 100%;
  position: relative;
  z-index: 100;
  min-height: 3.2rem;
}
.typeTabs .menusBut i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.typeTabs .menusAc {
  z-index: 80;
  background-color: #fff;
}
.typeTabs .allTabs {
  opacity: 0;
  z-index: -100;
  box-sizing: border-box;
  position: absolute;
  text-align: center;
  top: 3rem;
  width: 100%;
  left: 0;
  transform-origin: 0 0;
  transform: scale(1, 0);
  transition: 0.3s all;
  background: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  border: 1px solid rgba(0, 0, 0, 0.04);
  border-top: none;
}
.typeTabs .allTabs h2 {
  font-size: 1.3rem;
  color: #333;
  border-bottom: 1px solid #eee;
}
.typeTabs .allTabs .allTabsContent {
  max-height: 14rem;
  overflow-y: scroll;
}
.typeTabs .allTabs .allTabsContent .allTabsContentList {
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.04);
  border-right: 1px solid rgba(0, 0, 0, 0.04);
  width: 33.3333%;
  float: left;
}
.typeTabs .allTabs .allTabsContent .allTabsContentList .text {
  color: #666;
}
.typeTabs .allTabs .allTabsContent .allTabsContentListIcon {
  height: 7rem;
}
.typeTabs .allTabs .allTabsContent .allTabsContentListIcon .Icon {
  width: 100%;
  text-align: center;
  margin-top: 1rem;
}
.typeTabs .allTabs .allTabsContent .allTabsContentListIcon .Icon img {
  width: 50%;
}
.typeTabs .allTabs .allTabsContent .allTabsContentListIcon p {
  line-height: normal;
  margin-top: -1rem;
  margin-bottom: 0.5rem;
}
.typeTabs .allTabs .allTabsContent .allTabsContentList:nth-child(3n) {
  border-right: none;
}
.typeTabs .allTabsShow {
  opacity: 1;
  z-index: 79;
  transform: scale(1, 1);
}
.typeTabs .modal {
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -100;
}
.typeTabs .modalShow {
  z-index: 50;
}



