
.Service .Content {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  background-color: #f3f4f5;
}
.Service .Content .describe {
  height: 16.666rem;
  background-color: #fff;
  position: relative;
}
.Service .Content .describe .describeContent {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.Service .Content .describe h2 {
  font-size: 2rem;
  color: #333;
  text-align: center;
  margin-top: 1rem;
}
.Service .Content .describe h3 {
  color: #999999;
  font-size: 1.25rem;
  text-align: center;
  margin-top: 1.4rem;
}
.Service .Content .describe .cancelBut {
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #f06553;
  font-size: 0.8333rem;
  text-align: center;
}
.Service .Content .Add {
  margin-top: 1rem;
}
.Service .DjCancelOrder {
  border: 1px solid #eee;
  height: 10rem;
}
.Service .DjCancelOrder textarea {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: none;
  resize: none;
  font-size: 1rem;
  padding: 0.5rem;
}



