
.orderTabs {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  line-height: 3rem;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  z-index: 150;
}
.orderTabs .orderTabsContent {
  width: 100%;
  overflow: auto;
}
.orderTabs ul li {
  float: left;
  text-align: center;
}
.orderTabs ul li a {
  color: #333;
}
.orderTabs ul .Action a {
  color: #f06553;
}



