
.capitalRecordList {
  box-sizing: border-box;
  width: 100%;
  padding: 0 1.3rem;
  height: 7rem;
  position: relative;
}
.capitalRecordList .rows {
  width: 100%;
}
.capitalRecordList .time {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 0.3rem;
  position: relative;
}
.capitalRecordList .Type,
.capitalRecordList .Sum {
  font-size: 1.2rem;
  color: #999;
}
.capitalRecordList .Type {
  float: left;
}
.capitalRecordList .Sum {
  float: right;
}
.capitalRecordList .SumRed {
  color: #f06553;
}
.capitalRecordList:after {
  content: '';
  width: 1px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  left: 0;
  top: 0.6rem;
}
.capitalRecordList:before {
  content: '';
  width: 0.8rem;
  height: 0.8rem;
  background-color: #f06553;
  border-radius: 50%;
  position: absolute;
  left: -0.4rem;
  top: 0.55rem;
  z-index: 5;
}



