
.couponSelect {
  position: relative;
  width: 100%;
  height: 100%;
}
.couponSelect .couponSelectCont {
  width: 100%;
  height: 100%;
  position: relative;
  font-size: 0.8333rem;
}
.couponSelect .couponSelectCont .red {
  color: red;
  font-size: 1.25rem;
  margin: 0 0.3rem;
}
.couponSelect .couponSelectList {
  box-sizing: border-box;
  position: fixed;
  min-width: 85%;
  height: 50%;
  left: 50%;
  top: 50%;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  font-size: 1rem;
  color: #333;
  line-height: 3.5rem;
  transition: 0.3s all;
  transform-origin: 0 0;
  transform: translate(-50%, -50%) scale(1, 0.6);
  opacity: 0;
  z-index: -10;
  text-align: center;
}
.couponSelect .couponSelectList .Title {
  line-height: 3.5rem;
  border-radius: 0.333rem 0.333rem 0 0;
  overflow: hidden;
  color: #333;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  background: #fff;
  position: relative;
  z-index: 50;
}
.couponSelect .couponSelectList .Title h2 {
  color: #333333;
}
.couponSelect .couponSelectList .Content {
  height: calc(100% - 3.5rem);
  border-radius: 0 0 0.333rem 0.333rem;
  overflow-y: auto;
  background: #eee;
}
.couponSelect .couponSelectList ul {
  box-sizing: border-box;
  width: 100%;
  padding: 0 0.5rem;
  padding-bottom: 1rem;
}
.couponSelect .couponSelectList li {
  border-top: 1px solid #eee;
  padding: 0 0.5rem;
  line-height: 4.5rem;
  background: #fff;
  border-radius: 0.333rem;
  margin: 0.5rem 0;
}
.couponSelect .couponSelectList li:nth-last-child(1) {
  border-bottom: 1px solid #eee;
}
.couponSelect .couponSelectList .nonuseBut {
  line-height: 4rem;
}
.couponSelect .couponSelectList .acLi {
  background: rgba(0, 0, 0, 0.05);
}
.couponSelect .couponSelectList .name {
  float: left;
}
.couponSelect .couponSelectList .name span {
  vertical-align: middle;
}
.couponSelect .couponSelectList .name .badge {
  display: inline-block;
  width: 1.4rem;
  height: 1.4rem;
  text-align: center;
  line-height: 1.6rem;
  border-radius: 0.333rem;
  overflow: hidden;
  font-size: 0.8333rem;
  color: #fff;
  margin-right: 0.3rem;
}
.couponSelect .couponSelectList .name .deduction {
  background-color: #f02e1b;
}
.couponSelect .couponSelectList .name .discount {
  background-color: #f0a638;
}
.couponSelect .couponSelectList .name .text {
  display: inline-block;
  width: 5rem;
  max-width: 5rem;
  line-height: 4.5rem;
  height: 4.5rem;
  overflow: hidden;
  text-overflow: ellipsis;
}
.couponSelect .couponSelectList .time {
  float: left;
  margin-left: 1rem;
  color: #999;
  width: 8.5rem;
  text-align: right;
}
.couponSelect .couponSelectList .time span {
  margin-left: 0.5rem;
}
.couponSelect .couponSelectList .val {
  float: right;
  font-size: 0.833rem;
  color: #898989;
  width: auto !important;
  min-width: inherit !important;
  margin-top: -0.2rem;
}
.couponSelect .couponSelectList .red {
  color: red;
  font-size: 1.25rem;
  margin: 0 !important;
}
.couponSelect .couponSelectList .closeBut {
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  text-align: center;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  bottom: -1rem;
  left: 50%;
  transform: translate(-50%, 10%);
  z-index: 30;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
.couponSelect .couponSelectList .closeBut span {
  position: absolute;
  left: 50%;
  top: 50%;
  color: #666;
  transform: translate(-44%, -45%);
}
.couponSelect .module {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -10;
}
.couponSelectShow {
  z-index: 100;
}
.couponSelectShow .couponSelectCont {
  z-index: 10;
}
.couponSelectShow .couponSelectList {
  transform: translate(-50%, -50%) scale(1, 1);
  opacity: 1;
  z-index: 20;
}
.couponSelectShow .module {
  z-index: 10;
}



