
.HuoYun {
  min-height: 100%;
}
.HuoYun .content {
  padding-bottom: 0.5rem;
}
.HuoYun .content .banner {
  box-sizing: border-box;
  width: 100%;
  height: 5rem;
  border-radius: 0.333rem;
  padding: 0 1rem;
  overflow: hidden;
}
.HuoYun .content .contentList {
  position: relative;
  min-height: 30vh;
}
.HuoYun .content .contentList .row {
  width: 100%;
  box-sizing: border-box;
  position: relative;
  padding: 1rem;
  margin: 1rem 0;
  background: #fff;
}
.HuoYun .content .contentList .row .left {
  display: inline-block;
  width: 5.33rem;
  height: 5.33rem;
  margin-right: 1.3rem;
  float: left;
  border-radius: 50%;
  overflow: auto;
  position: absolute;
  left: 4rem;
  top: 50%;
  transform: translate(-50%, -50%);
}
.HuoYun .content .contentList .row .left img {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.HuoYun .content .contentList .row .right {
  width: calc(100% - 7rem);
  float: right;
}
.HuoYun .content .contentList .row .right .title {
  margin-bottom: 0.2rem;
}
.HuoYun .content .contentList .row .right .title h2 {
  line-height: 1.5rem;
  height: 1.5rem;
}
.HuoYun .content .contentList .row .right .title .name {
  display: inline-block;
  float: left;
  font-size: 1.4rem;
  width: 80%;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
}
.HuoYun .content .contentList .row .right .title .price {
  float: right;
  font-size: 1rem;
  margin-right: 0.5rem;
  color: #666;
}
.HuoYun .content .contentList .row .right .title .price span {
  color: #f06553;
}
.HuoYun .content .contentList .row .right .item {
  font-size: 1rem;
}
.HuoYun .content .contentList .row .right .item p {
  color: #666;
}
.HuoYun .content .contentList .row .icon {
  position: absolute;
  right: 0.5rem;
  top: 50%;
  transform: translate(0, -50%);
}
.HuoYun .content .contentList .row .icon i {
  color: rgba(0, 0, 0, 0.1);
}
.HuoYun .content .contentList .tishi {
  display: inline-block;
  width: 100%;
  margin-top: 20vh;
  position: absolute;
  text-align: center;
  font-size: 1.25rem;
}



