
.daijiaAwait .AwaitContent {
  height: 100vh;
  width: 100vw;
  box-sizing: border-box;
  overflow: hidden;
  overflow-y: auto;
  background-color: #f3f4f5;
}
.daijiaAwait .AwaitContent .page {
  margin-bottom: 1rem;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  text-align: center;
  padding: 2rem 0;
}
.daijiaAwait .AwaitContent .page .pageCarType {
  text-align: center;
  margin-top: 2.125rem;
}
.daijiaAwait .AwaitContent .page .pageCarType span {
  padding: 0 1rem;
  height: 1.8rem;
  line-height: 1.9rem;
  color: #fff;
  background-color: #f06553;
  font-size: 1rem;
  border-radius: 0.9rem;
}
.daijiaAwait .AwaitContent .page .upload {
  width: 100%;
  height: 11rem;
  text-align: center;
  margin-top: 0.625rem;
  position: relative;
}
.daijiaAwait .AwaitContent .page .upload .imgCircle img {
  height: 8rem;
  transform-origin: 50% 50%;
  transform: rotate(0);
}
.daijiaAwait .AwaitContent .page .upload .imgMan {
  position: absolute;
  top: 2.620rem;
  left: 0;
  width: 100%;
}
.daijiaAwait .AwaitContent .page .upload .imgMan img {
  display: inline-block;
  margin: 0 auto;
  height: 2.725rem;
}
.daijiaAwait .AwaitContent .page .upload .awaitText {
  margin-top: 0.725rem;
  font-size: 1.25rem;
  color: #222222;
}
.daijiaAwait .AwaitContent .page h2 {
  font-size: 0.8333rem;
  color: #999999;
  text-align: center;
}
.daijiaAwait .AwaitContent .page .cancelBut {
  color: #f06553;
  font-size: 0.8333rem;
  margin-top: 1.525rem;
}
.daijiaAwait .AwaitContent .page1 {
  text-align: center;
  padding: 1rem 2rem;
  padding-bottom: 2rem;
}
.daijiaAwait .DjCancelOrder {
  border: 1px solid #eee;
  height: 10rem;
}
.daijiaAwait .DjCancelOrder textarea {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: none;
  resize: none;
  font-size: 1rem;
  padding: 0.5rem;
}



