
.HuoYunOrderInfo {
  min-height: 100%;
}
.HuoYunOrderInfo .content {
  box-sizing: border-box;
  padding: 0 1rem;
  width: 100%;
}
.HuoYunOrderInfo .content .OrderForm {
  overflow: hidden;
  border-radius: 0.333rem;
  margin-top: 1rem;
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem;
  background: #fff;
}
.HuoYunOrderInfo .content .OrderForm .row {
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid #f2f2f2;
  line-height: 3.5rem;
  height: 3.5rem;
  padding: 0 1rem;
  font-size: 1.1666rem;
  position: relative;
}
.HuoYunOrderInfo .content .OrderForm .row i {
  float: left;
  margin-right: 0.5rem;
}
.HuoYunOrderInfo .content .OrderForm .row .icon-yuan {
  font-size: 1rem;
}
.HuoYunOrderInfo .content .OrderForm .row .val {
  min-width: 85%;
  max-width: 85%;
  display: inline-block;
  height: inherit;
  float: left;
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #333;
}
.HuoYunOrderInfo .content .OrderForm .row .weiVal {
  color: #999;
}
.HuoYunOrderInfo .content .OrderForm .row .line {
  position: absolute;
  width: 90%;
  right: 0;
  bottom: 0;
  height: 1px;
  background: #eee;
}
.HuoYunOrderInfo .content .OrderForm .row .pushPassAddrBut,
.HuoYunOrderInfo .content .OrderForm .row .removePassAddrBut {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.HuoYunOrderInfo .content .OrderForm .time i {
  font-size: 1rem;
  color: #fab10a;
}
.HuoYunOrderInfo .content .OrderForm .time input {
  border: none;
  font-size: 1.1666rem;
}
.HuoYunOrderInfo .content .OrderForm .startAdd {
  color: #3cafb9;
  border-bottom: none;
}
.HuoYunOrderInfo .content .OrderForm .endAdd i {
  color: #f06553;
}
.HuoYunOrderInfo .content .OrderForm .row:nth-last-child(1) {
  border-bottom: none;
}
.HuoYunOrderInfo .content .OrderForm .passAddrList .row {
  position: relative;
  border-bottom: none;
}
.HuoYunOrderInfo .content .OrderForm .passAddrList .row i {
  color: #666;
  position: relative;
  z-index: 5;
}
.HuoYunOrderInfo .content .OrderForm .passAddrList .row span {
  font-size: 1rem;
}
.HuoYunOrderInfo .content .OrderForm .passAddrList .row .placeholder {
  color: #999;
}
.HuoYunOrderInfo .content .OrderForm .passAddrList .row::after {
  content: '';
  height: 175%;
  width: 1px;
  background: #F3F4F5;
  position: absolute;
  left: 1.45rem;
  top: 50%;
  transform: translate(0, -50%);
}
.HuoYunOrderInfo .content .OrderForm .passAddrList .line {
  position: absolute;
  width: 90%;
  right: 0;
  bottom: 0;
  height: 1px;
  background: #eee;
}
.HuoYunOrderInfo .content .additionalServer {
  overflow: hidden;
  border-radius: 0.333rem;
  width: 100%;
  box-sizing: border-box;
  margin-top: 1rem;
  background: #fff;
  padding: 1rem 0.5rem;
}
.HuoYunOrderInfo .content .additionalServer .title {
  width: 100%;
  box-sizing: border-box;
  padding-left: 0.9rem;
  line-height: 2rem;
}
.HuoYunOrderInfo .content .additionalServer .title i {
  float: left;
  color: #cbcbcb;
}
.HuoYunOrderInfo .content .additionalServer .title h2 {
  margin-left: 0.5rem;
  float: left;
  font-size: 1rem;
  color: #666;
}
.HuoYunOrderInfo .content .additionalServer .Cont {
  padding-top: 0.5rem;
  width: 100%;
  text-align: center;
}
.HuoYunOrderInfo .content .additionalServer .Cont .radioBox {
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
  width: 29%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.15rem;
  position: relative;
  min-height: 3.6rem;
  height: 3.666rem;
  transition: 0.3s all;
}
.HuoYunOrderInfo .content .additionalServer .Cont .radioBox h2 {
  font-size: 1rem;
  color: #333;
}
.HuoYunOrderInfo .content .additionalServer .Cont .radioBox p {
  margin-top: 0.2rem;
  font-size: 0.8333rem;
  color: #999;
}
.HuoYunOrderInfo .content .additionalServer .Cont .radioBox .val {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}
.HuoYunOrderInfo .content .additionalServer .Cont .radioBox .Icon {
  position: absolute;
  right: 0.15rem;
  bottom: -1px;
  z-index: 15;
}
.HuoYunOrderInfo .content .additionalServer .Cont .radioBox .Icon i {
  color: #fff;
  font-size: 5px;
  font-weight: bold;
}
.HuoYunOrderInfo .content .additionalServer .Cont .receipt {
  margin: 0 1.8%;
}
.HuoYunOrderInfo .content .additionalServer .Cont .radioBoxAc {
  overflow: hidden;
  position: relative;
  border: 1px solid #f06553;
}
.HuoYunOrderInfo .content .additionalServer .Cont .radioBoxAc::after {
  content: '';
  position: absolute;
  right: -1.25rem;
  bottom: -1.25rem;
  transform-origin: 50% 50%;
  transform: rotate(45deg);
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  z-index: 10;
  background: #f06553;
}
.HuoYunOrderInfo .content .remark {
  width: 100%;
  box-sizing: border-box;
  background: #fff;
  margin-top: 1rem;
  padding: 1rem 0.5rem;
  padding-left: 1.5rem;
  overflow: hidden;
  border-radius: 0.333rem;
}
.HuoYunOrderInfo .content .remark i {
  float: left;
  color: #cbcbcb;
}
.HuoYunOrderInfo .content .remark .val {
  float: left;
  margin-left: 0.4rem;
  font-size: 1rem;
  color: #666;
}
.HuoYunOrderInfo .content .bottom {
  width: 100%;
  box-sizing: border-box;
  margin-top: 1rem;
}
.HuoYunOrderInfo .content .bottom .row {
  border-radius: 0.333rem;
  box-sizing: border-box;
  padding: 0.5rem 1rem;
  width: 100%;
  position: relative;
  background: #fff;
  line-height: 3rem;
}
.HuoYunOrderInfo .content .bottom .row .left {
  float: left;
  width: 50%;
  text-align: center;
  font-size: 1rem;
  color: #333;
}
.HuoYunOrderInfo .content .bottom .row .left span {
  font-size: 2rem;
  color: #f06553;
  margin: 0 0.2rem;
}
.HuoYunOrderInfo .content .bottom .row .left i {
  font-size: 0.8333rem;
  color: rgba(0, 0, 0, 0.3);
}
.HuoYunOrderInfo .content .bottom .row .right {
  width: 50%;
  float: right;
  text-align: center;
  font-size: 1rem;
  color: #333;
}
.HuoYunOrderInfo .content .bottom .row .line {
  width: 1px;
  height: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: #eee;
  z-index: 10;
}
.HuoYunOrderInfo .content .bottom .submitOrderBut {
  width: 100%;
  background: #f06553;
  color: #fff;
  text-align: center;
  line-height: 3.75rem;
  height: 3.75rem;
  border-radius: 0.333rem;
  margin-top: 2rem;
}
.HuoYunOrderInfo .inputBoxSite .siteInput .siteInputShow {
  opacity: 0;
  z-index: -100;
  position: relative;
}
.HuoYunOrderInfo .inputBox .HYAdd .row {
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid #f2f2f2;
  line-height: 3.5rem;
  height: 3.5rem;
  padding: 0 1rem;
  font-size: 1rem;
  position: relative;
}
.HuoYunOrderInfo .inputBox .HYAdd .row i {
  float: left;
  margin-right: 0.5rem;
  color: #999999;
}
.HuoYunOrderInfo .inputBox .HYAdd .row .icon-yuan {
  font-size: 1rem;
}
.HuoYunOrderInfo .inputBox .HYAdd .row .val {
  display: inline-block;
  width: auto;
  height: 3.5rem;
  min-width: 85%;
  max-width: 85%;
  overflow: hidden;
  text-overflow: ellipsis;
  float: left;
}
.HuoYunOrderInfo .inputBox .HYAdd .row .pushPassAddrBut,
.HuoYunOrderInfo .inputBox .HYAdd .row .removePassAddrBut {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.HuoYunOrderInfo .inputBox .HYAdd .row input {
  border: none;
  font-size: 1rem;
  width: 100%;
}
.HuoYunOrderInfo .remarkInput textarea {
  width: 100%;
  box-sizing: border-box;
  padding: 0.5rem;
  resize: none;
  min-height: 8rem;
  max-height: 10rem;
  border: 1px solid #eee;
}
.HuoYunOrderInfo .receivedPayBox {
  margin: 1rem 0;
  box-sizing: border-box;
  width: 100%;
  border: 1px solid #eee;
  padding: 0 0.5rem;
  margin-bottom: 2rem;
}
.HuoYunOrderInfo .receivedPayBox input {
  width: 100%;
  border: none;
  font-size: 1rem;
  line-height: 2.5rem;
}
.HuoYunOrderInfo .costBox ul li {
  height: 2rem;
  line-height: 2rem;
  color: #999999;
}
.HuoYunOrderInfo .costBox ul li .name {
  float: left;
}
.HuoYunOrderInfo .costBox ul li .time,
.HuoYunOrderInfo .costBox ul li .val {
  float: right;
}
.HuoYunOrderInfo .costBox ul li .time {
  margin-right: 0.8rem;
  color: #666;
}
.HuoYunOrderInfo .costBox ul li .sum {
  color: #f06553;
}
.HuoYunOrderInfo .costBox ul li .unit {
  margin-left: 0.3rem;
}
.HuoYunOrderInfo .costBox .introduce {
  border-top: 1px solid #eee;
  margin-top: 0.5rem;
  padding-top: 1.2rem;
  line-height: 1.8rem;
  color: #666;
}



