
.coupon {
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  overflow: hidden;
  background-color: #f3f4f5;
}
.coupon .couponContent {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  padding-top: 3.6rem;
}
.coupon .couponContent .loadMoreBut {
  width: 100%;
  line-height: 3rem;
  padding-bottom: 1rem;
}
.coupon .couponContent .loadMoreBut span {
  width: 100%;
  line-height: 3rem;
  color: #666;
  background-color: #fff;
  border-radius: 0.333rem;
  text-align: center;
}



