
.capitalRecord {
  padding-bottom: 1rem;
}
.capitalRecord .content {
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem;
  padding-top: 0.5rem;
}
.capitalRecord .content .capitalRecordList:nth-last-child(1):after {
  opacity: 0;
}
.capitalRecord .content .zanwujilv {
  padding-top: 30vh;
}
.capitalRecord .content .zanwujilv h2 {
  text-align: center;
}
.capitalRecord .queryList {
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem;
  line-height: 3rem;
  height: 3rem;
}
.capitalRecord .queryList span {
  box-sizing: border-box;
  width: 100%;
  text-align: center;
  color: #fff;
  background-color: #f06553;
  border-radius: 0.333rem;
}



