
.XK-input {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  position: relative;
  padding: 0 0.5rem;
}
.XK-input .closeButDiv {
  right: 0;
  height: 100%;
  width: 1.5rem;
  top: 0;
  position: absolute;
}
.XK-input .closeButDiv .closeBut {
  position: absolute;
  background-color: #ddd;
  right: 0.8rem;
  top: 50%;
  transform: translate(0, -50%);
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
}
.XK-input .closeButDiv .closeBut i {
  position: absolute;
  font-size: 0.4rem !important;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
}
.XK-input input {
  height: 100%;
  box-sizing: border-box;
  line-height: 1;
  width: 100%;
  padding-right: 1.5rem;
  border: none;
}



