
.couponNav {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 150;
  line-height: 3.6rem;
  background-color: #fff;
  width: 100%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
.couponNav li {
  display: inline-block;
  width: 33.333%;
  text-align: center;
}
.couponNav li a {
  color: #333;
  font-size: 1.25rem;
}
.couponNav .actionLi a {
  color: #f06553;
}



