
.share {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: url(../../assets/img/erweima.png) no-repeat center #fe4e4e;
  background-size: 100%;
  position: relative;
}
.share .QrCode {
  position: absolute;
  left: 50%;
  top: 52%;
  transform: translate(-50%, -50%);
  width: 50%;
  border-radius: 0.333rem;
  overflow: hidden;
}
.share .QrCode img {
  width: 100%;
  border-radius: 0.333rem;
}



