
#myPicker {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 500;
  left: 0;
  bottom: 0;
}
#myPicker .moda {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
}
#myPicker .lines {
  width: 100%;
  height: 30px;
  border-top: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
  position: absolute;
  top: 8.2rem;
  left: 0;
}
#myPicker .pickerContainer {
  background-color: #ffffff;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 200px;
  z-index: 100;
}
#myPicker .pickerContainer .bar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  color: #000;
  font-size: 1.2rem;
  height: 2rem;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.5rem 0;
}
#myPicker .pickerContainer .bar .sureBtn {
  color: #f06553;
}
#myPicker .pickerContainer .bar .cancelBtn {
  color: #999999;
}
#myPicker .pickerContainer .picker {
  display: block;
  position: absolute;
  left: 0;
  top: 30px;
}
#myPicker .pickerContainer .picker .picker-item {
  font-size: 1.2rem;
  color: #999999;
}
#myPicker .pickerContainer .picker .picker-selected {
  color: #000000;
}



