
.unfilledOrderMod {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 300;
}
.unfilledOrderMod .Content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 60%;
  background-color: #eee;
  border-radius: 0.333rem;
  z-index: 50;
}
.unfilledOrderMod .Content .Title {
  text-align: center;
  line-height: 3.5rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 0.333rem 0.333rem 0 0;
  background-color: #fff;
}
.unfilledOrderMod .Content .Title h2 {
  color: #333;
}
.unfilledOrderMod .Content .List {
  box-sizing: border-box;
  width: 100%;
  height: calc(100% - 3.5rem);
  overflow-y: auto;
}
.unfilledOrderMod .Content .closeBut {
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  text-align: center;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  bottom: -1rem;
  left: 50%;
  transform: translate(-50%, 10%);
  z-index: 80;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
.unfilledOrderMod .Content .closeBut span {
  position: absolute;
  left: 50%;
  top: 50%;
  color: #666;
  transform: translate(-44%, -45%);
}
.unfilledOrderMod .modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
}



