
.orderRecord {
  width: 100vw;
  height: 100vh;
  background-color: #f3f4f5;
}
.orderRecord .orderRecordNav {
  height: 3rem;
}
.orderRecord .contentList {
  box-sizing: border-box;
  width: 100%;
  padding: 0 0.5rem;
  overflow: hidden;
  overflow-y: auto;
}
.orderRecord .ListItem {
  box-sizing: border-box;
  width: calc(100% - 1rem);
  margin: 1rem 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #fff;
  border-radius: 0.33333rem;
  overflow: hidden;
}
.orderRecord .ListItem .itemContent .row {
  height: 2rem;
  line-height: 2rem;
  overflow: hidden;
}
.orderRecord .ListItem .itemContent .row1 {
  margin-bottom: 0.5rem;
}
.orderRecord .ListItem .itemContent .row1 .itemTime {
  float: left;
  display: inline-block;
  font-size: 1rem;
  color: #333;
}
.orderRecord .ListItem .itemContent .row1 .itemStatus {
  float: right;
  display: inline-block;
}
.orderRecord .ListItem .itemContent .row1 .itemStatus .text {
  float: right;
  color: #999999;
  margin-right: 0.5rem;
}
.orderRecord .ListItem .itemContent .row1 .itemStatus .textRed {
  color: #f06553;
}
.orderRecord .ListItem .itemContent .row1 i {
  font-size: 0.8333rem;
  color: #999999;
  float: right;
}
.orderRecord .ListItem .itemContent .row2,
.orderRecord .ListItem .itemContent .row3 {
  color: #666666;
  font-size: 0.83333rem;
}
.orderRecord .ListItem .itemContent .row2 i,
.orderRecord .ListItem .itemContent .row3 i {
  line-height: 1.8rem;
  font-size: 0.66666rem;
  float: left;
  margin-right: 0.3333rem;
}
.orderRecord .ListItem .itemContent .row2 i {
  color: #3cafb9;
}
.orderRecord .ListItem .itemContent .row3 i {
  color: #f06553;
}
.orderRecord .NullOrder {
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}
.orderRecord .NullOrder .img {
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 50%;
}
.orderRecord .NullOrder .img img {
  width: 100%;
}
.orderRecord .NullOrder .img a {
  margin-top: 1rem;
  padding: 0 1rem;
  line-height: 2rem;
  border-radius: 1rem;
  border: 1px solid #f06553;
  color: #f06553;
  text-align: center;
}



