
.recharge {
  width: 100vw;
  height: 100vh;
  background-color: #f3f4f5;
  box-sizing: border-box;
  padding: 1rem;
}
.recharge .inform {
  width: 100%;
  box-sizing: border-box;
  padding: 0 1rem;
  background-color: #fff;
  margin-bottom: 1rem;
  border-radius: 0.333rem;
  overflow: hidden;
  height: 2.9rem;
  line-height: 2.9rem;
}
.recharge .inform .list {
  width: 60%;
  box-sizing: border-box;
  float: left;
  padding-left: 1rem;
}
.recharge .inform .list .text {
  font-size: 1rem;
  color: #333333;
  text-align: left;
  width: 3rem;
}
.recharge .inform .list .val {
  font-size: 0.8333rem;
  color: #999999;
  width: calc(100% - 4.5rem);
}
.recharge .inform .list:nth-child(2) {
  width: 40%;
  padding-left: 0;
}
.recharge .inform .list:nth-child(2) .text {
  text-align: left;
  width: 3rem;
}
.recharge .selectSum {
  box-sizing: border-box;
  background-color: #fff;
  width: 100%;
  border-radius: 0.333rem;
  padding: 0 1rem;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}
.recharge .selectSum h2 {
  font-size: 1rem;
  line-height: 2.9166rem;
  border-bottom: 1px solid #eeeeee;
  padding-left: 1rem;
}
.recharge .selectSum h2 .val {
  color: #f06553;
  margin: 0 0.5rem;
}
.recharge .selectSum h2 .unit {
  color: #999;
}
.recharge .selectSum .content {
  box-sizing: border-box;
  width: 100%;
  padding: 1rem;
}
.recharge .selectSum .content .default {
  margin-bottom: 0.5rem;
}
.recharge .selectSum .content .default .tab {
  box-sizing: border-box;
  width: calc(25% - 0.5rem);
  font-size: 0.833rem;
  color: #333333;
  line-height: 2.833rem;
  height: 2.8333rem;
  border-radius: 0.333rem;
  border: 1px solid #eee;
  text-align: center;
  margin-right: 0.5rem;
  float: left;
  transition: 0.3s all;
  margin-bottom: 0.5rem;
}
.recharge .selectSum .content .default .tabAc {
  background-color: #f06553;
  color: #fff;
  border-color: #f06553;
}
.recharge .selectSum .content .customSum {
  height: 3.666rem;
  line-height: 3.666rem;
  border: 1px solid #eee;
  width: 100%;
  border-radius: 0.333rem;
  overflow: hidden;
}
.recharge .selectSum .content .customSum input {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 0 0.5rem;
  border: none;
  font-size: 0.8333rem;
}
.recharge .payType {
  box-sizing: border-box;
  width: 100%;
  background-color: #fff;
  padding: 0 1rem;
  margin-bottom: 1rem;
  border-radius: 0.333rem;
}
.recharge .payType h2 {
  font-size: 1rem;
  line-height: 2.9166rem;
  border-bottom: 1px solid #eeeeee;
  padding-left: 1rem;
}
.recharge .payType .content .row {
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  height: 3.666rem;
  line-height: 3.666rem;
  border-bottom: 1px solid #eee;
  padding-left: 1rem;
  position: relative;
}
.recharge .payType .content .row .text {
  padding-left: 1.8rem;
  display: inline-block;
}
.recharge .payType .content .row .select {
  float: right;
  width: 1.166rem;
  height: 1.166rem;
  border: 1px solid #eee;
  border-radius: 50%;
  overflow: hidden;
  vertical-align: middle;
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translate(0, -50%);
}
.recharge .payType .content .row .select i {
  font-size: 0.5rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}
.recharge .payType .content .row .selectAc {
  background-color: #f06553;
  border-color: #f06553;
  color: #fff;
}
.recharge .payType .content .row .selectAc i {
  opacity: 1;
}
.recharge .payType .content .row:nth-child(1) .text {
  background: url(../../assets/img/recharge/wx.png) no-repeat left center;
  background-size: 1.475rem 1.3rem;
}
.recharge .payType .content .row:nth-child(2) .text {
  background: url(../../assets/img/recharge/yinlian.png) no-repeat left center;
  background-size: 1.375rem 1.3rem;
}
.recharge .payType .content .row:nth-last-child(1) {
  border: none;
}
.recharge .rechargeBut span {
  width: 100%;
  border-radius: 0.333rem;
  background-color: #e1e1e1;
  color: #cbcbcb;
  height: 3.75rem;
  line-height: 3.75rem;
  text-align: center;
  font-size: 1rem;
  transition: 0.3s all;
}
.recharge .rechargeButShow span {
  background-color: #f06553;
  color: #fff;
}



