
.login {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -100;
  opacity: 0;
  transition: 0.3s all;
}
.login .forget {
  color: #f06553;
  font-size: 0.8333rem;
  display: inline-block;
}
.login .nextStep {
  font-size: 0.8333rem;
  display: inline-block;
}
.login .loginContent {
  padding-top: 1rem;
  width: 19.75rem;
  height: auto;
  background-color: #fff;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 0.333rem;
  transition: 0.3s all;
  z-index: 777;
}
.login .loginContent .row {
  width: 100%;
  box-sizing: border-box;
  height: 2.9166rem;
  line-height: 2.9166rem;
  padding: 0 1rem;
  margin: 1rem 0;
}
.login .loginContent .row1 .content {
  width: 100%;
  border: 1px solid #eee;
  border-radius: 0.333rem;
}
.login .loginContent .row1 .Region {
  float: left;
  height: 100%;
  position: relative;
  width: 5.8333rem;
  text-align: center;
}
.login .loginContent .row1 .Region ul {
  position: absolute;
  left: 0;
  top: 3rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  border-radius: 0.333rem;
  background-color: #fff;
  width: 100%;
  z-index: -100;
  font-size: 1rem;
  color: #2c2c2c;
  transform-origin: 0 0;
  opacity: 0;
}
.login .loginContent .row1 .Region ul li {
  width: 100%;
}
.login .loginContent .row1 .Region .showUl {
  z-index: 10;
  opacity: 1;
}
.login .loginContent .row1 .Region:after {
  content: '';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  width: 1px;
  height: 50%;
  background-color: #eee;
}
.login .loginContent .row1 .phone {
  box-sizing: border-box;
  height: 100%;
  width: calc(100% - 5.8333rem);
}
.login .loginContent .row1 .phone input {
  box-sizing: border-box;
  width: 100%;
  border: none;
  padding: 0 0.5rem;
  height: 100%;
  font-size: 1rem;
  color: #2c2c2c;
}
.login .loginContent .row2 .numCode {
  height: 100%;
  width: calc(100% - 7.25rem);
  float: left;
  box-sizing: border-box;
  border: 1px solid #eee;
  border-radius: 0.333rem;
  overflow: hidden;
}
.login .loginContent .row2 .numCode input {
  font-size: 1rem;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  border: none;
  color: #2c2c2c;
  padding: 0 0.5rem;
}
.login .loginContent .row2 .numCodeBut {
  box-sizing: border-box;
  width: 6.25rem;
  font-size: 0.8333rem;
  float: left;
  height: 100%;
  background-color: #f4f4f4;
  border-radius: 0.333rem;
  overflow: hidden;
  margin-left: 1rem;
  color: #999;
  transition: 0.3s all;
  text-align: center;
  border: 1px solid #eee;
}
.login .loginContent .row2 .numCodeButShow {
  color: #fff;
  background: #f06553;
}
.login .loginContent .row3 {
  height: auto;
  line-height: inherit;
}
.login .loginContent .row3 .top {
  height: 2.9166rem;
}
.login .loginContent .row3 .top .imgCode {
  float: left;
  height: 100%;
  width: calc(100% - 7.25rem);
}
.login .loginContent .row3 .top .imgCode input {
  box-sizing: border-box;
  width: 100%;
  padding: 0 0.5rem;
  height: 100%;
  border: 1px solid #eee;
  font-size: 1rem;
  border-radius: 0.333rem;
}
.login .loginContent .row3 .top .img {
  float: left;
  box-sizing: border-box;
  width: 6.25rem;
  font-size: 0.8333rem;
  height: 100%;
  background-color: #f4f4f4;
  border-radius: 0.333rem;
  overflow: hidden;
  margin-left: 1rem;
  color: #333333;
  text-align: center;
  border: 1px solid #eee;
}
.login .loginContent .row3 .top .img img {
  width: 100%;
  height: 100%;
}
.login .loginContent .row3 .bottom {
  width: 100%;
  text-align: center;
  font-size: 0.8333rem;
  margin-top: 0.3rem;
  color: #999999;
}
.login .loginContent .row5 .password {
  height: 100%;
  width: 100%;
  float: left;
  box-sizing: border-box;
  border: 1px solid #eee;
  border-radius: 0.333rem;
  overflow: hidden;
}
.login .loginContent .row5 .password input {
  font-size: 1rem;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  border: none;
  color: #2c2c2c;
  padding: 0 0.5rem;
}
.login .loginContent .row4 {
  line-height: inherit;
  height: auto;
  margin-top: 1.6rem;
  font-size: 0.8333rem;
  color: #a5a5a5;
}
.login .loginContent .row4 .protocol {
  color: #f06553;
}
.login .loginContent .loginBut {
  height: 3.4rem;
  line-height: 3.4rem;
  background-color: #f4f4f4;
  color: #bcbcbc;
  text-align: center;
  font-size: 1.25rem;
  border-radius: 0 0 0.333rem 0.333rem;
  transition: 0.3s all;
}
.login .loginContent .loginButShow {
  background-color: #f06553;
  color: #fff;
}
.login .Module {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 8888;
}
.showLogin {
  z-index: 555;
  opacity: 1;
}



