
.sideMenus {
  position: fixed;
  left: -110%;
  top: 0;
  height: 100%;
  z-index: -999;
  transition: 0.3s all;
}
.sideMenus .sideMenusContent {
  box-sizing: border-box;
  width: 80vw;
  height: 100vh;
  background: #fff;
  position: relative;
  z-index: 555;
  padding-top: 5.25rem;
}
.sideMenus .sideMenusContent .personInfo {
  padding: 0 1rem;
  height: 7.375rem;
  width: 100%;
  box-sizing: border-box;
}
.sideMenus .sideMenusContent .personInfo .headImg {
  box-sizing: border-box;
  width: 7.375rem;
  height: 7.375rem;
  border-radius: 50%;
  border: 1px solid #eee;
  overflow: hidden;
  float: left;
}
.sideMenus .sideMenusContent .personInfo .headImg img {
  width: 100%;
  height: 100%;
}
.sideMenus .sideMenusContent .personInfo .info {
  box-sizing: border-box;
  width: calc(100% - 7.375rem);
  float: left;
  padding-left: 1.3rem;
  padding-top: 1rem;
}
.sideMenus .sideMenusContent .personInfo .info .infoName {
  font-size: 1.25rem;
  color: #333;
  line-height: 2rem;
  font-weight: bold;
}
.sideMenus .sideMenusContent .personInfo .info .vipRank {
  font-size: 1rem;
  line-height: 1.66660rem;
  color: #363636;
}
.sideMenus .sideMenusContent .personInfo .info .phone {
  color: #999;
}
.sideMenus .sideMenusContent .menusOption {
  margin-top: 5rem;
  width: 100%;
  box-sizing: border-box;
}
.sideMenus .sideMenusContent .menusOption ul {
  width: 100%;
}
.sideMenus .sideMenusContent .menusOption ul li {
  height: 3.25rem;
  width: 100%;
  box-sizing: border-box;
  padding: 0 1rem;
}
.sideMenus .sideMenusContent .menusOption ul li a {
  width: 100%;
  color: #333;
  height: 100%;
  line-height: 2.4;
}
.sideMenus .sideMenusContent .menusOption ul li a .text {
  font-size: 1.25rem;
}
.sideMenus .sideMenusContent .menusOption ul li a i {
  margin-right: 1.0833rem;
}
.sideMenus .sideMenusContent .menusOption ul li a .icon-erweima {
  font-size: 1.2rem;
  margin-left: -0.1rem;
  margin-right: 0.9rem;
}
.sideMenus .sideMenusContent .bottomMeuns {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 5.08333rem;
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem;
  background: url(../../../../assets/img/yonghu/bottom_bg.png) no-repeat center bottom;
  background-size: auto 100%;
}
.sideMenus .sideMenusContent .bottomMeuns .content {
  height: 100%;
}
.sideMenus .sideMenusContent .bottomMeuns .content .informBut a,
.sideMenus .sideMenusContent .bottomMeuns .content .setBut a,
.sideMenus .sideMenusContent .bottomMeuns .content .shareBut a,
.sideMenus .sideMenusContent .bottomMeuns .content .contentText a {
  border-radius: 50%;
  display: inline-block;
}
.sideMenus .sideMenusContent .bottomMeuns .content .informBut,
.sideMenus .sideMenusContent .bottomMeuns .content .setBut {
  position: absolute;
  top: 50%;
  transform: translate(0, -45%);
}
.sideMenus .sideMenusContent .bottomMeuns .content .informBut a,
.sideMenus .sideMenusContent .bottomMeuns .content .setBut a {
  width: 3.33333rem;
  height: 3.33333rem;
  border: 1px solid #d4d4d4;
  background: #fff;
  border-radius: 50%;
  position: relative;
}
.sideMenus .sideMenusContent .bottomMeuns .content .informBut a i,
.sideMenus .sideMenusContent .bottomMeuns .content .setBut a i {
  font-size: 1.4rem;
  position: absolute;
  left: 50%;
  top: 50%;
  color: #333;
  transform: translate(-50%, -50%);
}
.sideMenus .sideMenusContent .bottomMeuns .content .informBut {
  left: 1.43333rem;
}
.sideMenus .sideMenusContent .bottomMeuns .content .informBut .badgeMod {
  position: absolute;
  right: -0.2rem;
  top: -0.2rem;
  border: 2px solid #E9E7E5;
}
.sideMenus .sideMenusContent .bottomMeuns .content .setBut {
  right: 1.83333rem;
}
.sideMenus .sideMenusContent .bottomMeuns .content .shareBut {
  position: absolute;
  top: -2.4rem;
  left: 50%;
  transform: translate(-51%, 0);
  width: 5rem;
  height: 5rem;
  background: #f9554d;
  border-radius: 50%;
  overflow: hidden;
}
.sideMenus .sideMenusContent .bottomMeuns .content .shareBut a {
  width: 100%;
  height: 100%;
}
.sideMenus .sideMenusContent .bottomMeuns .content .shareBut a i {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 3rem;
}
.sideMenus .sideMenusContent .bottomMeuns .content .contentText {
  position: absolute;
  bottom: 0.4rem;
  left: 50%;
  transform: translate(-50%, 0);
  color: #514f4c;
  font-size: 1rem;
}
.sideMenus .modal {
  width: 200%;
  height: 200%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -100;
  transition: 0.3s all;
}
.showSideMenus {
  left: 0;
  top: 0;
  z-index: 999;
}
.showSideMenus .modal {
  opacity: 1;
  z-index: 300;
}



