
.checkOrder .money {
  padding-top: 3rem;
  text-align: center;
}
.checkOrder .money h2 {
  font-size: 1.7rem;
  text-align: center;
  color: #f06553;
}
.checkOrder .money h2 .max {
  font-size: 3.2rem;
}
.checkOrder .money .detail {
  line-height: 1.2rem;
  text-align: center;
  color: #878787;
}
.checkOrder .money .score {
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.checkOrder .money .label {
  margin: 0 auto;
  margin-top: 1rem;
  width: 80%;
  text-align: center;
}
.checkOrder .money .label .TagItem {
  box-sizing: border-box;
  width: calc(50% - 1rem);
  border-radius: 0;
  background: rgba(0, 0, 0, 0.04);
  font-size: 1rem;
  color: #999;
  border: none;
  line-height: 2.5rem;
  height: 2.5rem;
  margin: 0.3rem 0.5rem;
}
.checkOrder .money .reviewContent {
  margin: 0 auto;
  box-sizing: border-box;
  width: 80%;
  margin-top: 1rem;
  line-height: 1.5rem;
  padding: 0.5rem;
  min-height: 5rem;
  border-radius: 0.333rem;
  text-align: center;
  color: #999;
}
.checkOrder .costBox ul li {
  height: 2rem;
  line-height: 2rem;
  color: #999999;
}
.checkOrder .costBox ul li .name {
  float: left;
}
.checkOrder .costBox ul li .time,
.checkOrder .costBox ul li .val {
  float: right;
}
.checkOrder .costBox ul li .time {
  margin-right: 0.8rem;
  color: #333333;
}
.checkOrder .costBox ul li .sum {
  color: #f06553;
}
.checkOrder .costBox ul li .unit {
  margin-left: 0.3rem;
}



