
.zhuanxianAwait .AwaitContent {
  height: 100vh;
  width: 100vw;
  box-sizing: border-box;
  overflow: hidden;
  overflow-y: auto;
  background-color: #f3f4f5;
}
.zhuanxianAwait .AwaitContent .page {
  margin-bottom: 1rem;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  text-align: center;
  padding: 2rem 0;
}
.zhuanxianAwait .AwaitContent .page .pageCarType {
  text-align: center;
  margin-top: 2.125rem;
}
.zhuanxianAwait .AwaitContent .page .pageCarType span {
  padding: 0 1rem;
  height: 1.8rem;
  line-height: 1.9rem;
  color: #fff;
  background-color: #f06553;
  font-size: 1rem;
  border-radius: 0.9rem;
}
.zhuanxianAwait .AwaitContent .page .upload {
  width: 100%;
  height: 7rem;
  text-align: center;
  margin-top: 3.125rem;
  position: relative;
}
.zhuanxianAwait .AwaitContent .page .upload img {
  height: 7rem;
  transform-origin: 50% 50%;
  transform: rotate(0);
}
.zhuanxianAwait .AwaitContent .page .upload .text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #999999;
  font-size: 1rem;
}
.zhuanxianAwait .AwaitContent .page h2 {
  font-size: 1.25rem;
  color: #333;
  margin-top: 2.7rem;
  text-align: center;
}
.zhuanxianAwait .AwaitContent .page .cancelBut {
  color: #f06553;
  font-size: 1rem;
  margin-top: 1rem;
}
.zhuanxianAwait .AwaitContent .page1 {
  text-align: center;
  padding: 1rem 2rem;
  padding-bottom: 2rem;
}
.zhuanxianAwait .ZcCancelOrder {
  border: 1px solid #eee;
  height: 10rem;
}
.zhuanxianAwait .ZcCancelOrder textarea {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: none;
  resize: none;
  font-size: 1rem;
  padding: 0.5rem;
}



