
.checkInput .inputImg {
  box-sizing: content-box;
  display: inline-block;
  float: left;
  margin-right: 0.3rem;
  position: relative;
  width: 1rem;
  height: 2.8rem;
}
.checkInput .inputImg img {
  position: absolute;
  width: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.checkInput .val {
  float: left;
  width: auto !important;
}



