
.PaoTui .content {
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem;
}
.PaoTui .content .banner {
  width: 100%;
  height: 5rem;
  border-radius: 0.333rem;
  overflow: hidden;
}
.PaoTui .content .business {
  width: 100%;
  padding-bottom: 1rem;
}
.PaoTui .content .business .list {
  box-sizing: border-box;
  width: 33.333%;
  float: left;
  text-align: center;
  margin-top: 3rem;
}
.PaoTui .content .business .list .icon {
  width: 4.75rem;
  height: 4.75rem;
  position: relative;
}
.PaoTui .content .business .list .icon img {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.PaoTui .content .business .list p {
  margin-top: 0.8rem;
  font-size: 1rem;
  color: #333;
}
.PaoTui .content .tishi {
  margin-top: 20vh;
  display: inline-block;
  width: 100%;
  text-align: center;
  font-size: 1.25rem;
}



