
.project {
  background-color: #f3f4f5;
  box-sizing: border-box;
  height: 100%;
  width: 100%;
}
.project .topNav {
  width: 100%;
  max-height: 100%;
  position: fixed;
  z-index: 250;
  left: 0;
  top: 0;
  box-sizing: border-box;
  padding: 0 1rem;
  background: #fff;
  line-height: 3.6rem;
}
.project .topNav .menuBut {
  width: 1rem;
  float: left;
}
.project .topNav .menuBut i {
  font-size: 1.25rem;
}
.project .topNav .yewu {
  float: left;
  margin-left: 1.5rem;
  width: calc(100% - 2.5rem);
}
.project .projectContent {
  background-color: #f3f4f5;
  box-sizing: border-box;
  padding-top: 5rem;
  height: 100vh;
  /*overflow-y: scroll;*/
}



