
.invoiceOrderRecord {
  width: 100%;
  box-sizing: border-box;
  height: 100vh;
  padding-bottom: 7rem;
}
.invoiceOrderRecord .invoiceOrderRecord-row {
  background: #fff;
  margin: 0 .5rem;
  border-radius: .4rem;
  margin-top: .5rem;
  padding: .8rem 1.2rem;
  padding-left: 3rem;
  position: relative;
}
.invoiceOrderRecord .invoiceOrderRecord-row .row-col {
  width: 80%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1.6rem;
}
.invoiceOrderRecord .invoiceOrderRecord-row .row-col .iconfont {
  vertical-align: middle;
  font-size: 1rem;
}
.invoiceOrderRecord .invoiceOrderRecord-row .row-col .icon-shijian1 {
  /*color: #9d9d9d;*/
}
.invoiceOrderRecord .invoiceOrderRecord-row .row-col .text {
  vertical-align: middle;
}
.invoiceOrderRecord .invoiceOrderRecord-row .row-money {
  position: absolute;
  right: .5rem;
  top: 50%;
  transform: translate(0, -50%);
}
.invoiceOrderRecord .invoiceOrderRecord-row .row-check {
  position: absolute;
  left: .5rem;
  top: 50%;
  transform: translate(0, -50%);
  width: 1.4rem;
  height: 1.4rem;
  line-height: 1.4rem;
  text-align: center;
  border-radius: 50%;
  border: 1px solid #eee;
  background: #fff;
  transition: .3s all;
}
.invoiceOrderRecord .invoiceOrderRecord-row .row-check i {
  color: #fff;
  font-size: .6rem;
}
.invoiceOrderRecord .invoiceOrderRecord-row[ac=true] .row-check {
  background: #f06553;
  border-color: #f06553;
}
.invoiceOrderRecord .panel {
  box-sizing: border-box;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
  padding: .5rem;
  padding-bottom: 0;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.12);
}
.invoiceOrderRecord .panel .panel-top {
  border-bottom: 1px solid #eee;
  line-height: 2rem;
  padding: 0 .5rem;
}
.invoiceOrderRecord .panel .panel-bottom {
  line-height: 4rem;
  padding: 0 .5rem;
  position: relative;
  font-size: 1.2rem;
}
.invoiceOrderRecord .panel .all-check {
  width: 1.4rem;
  height: 1.4rem;
  line-height: 1.4rem;
  text-align: center;
  border-radius: 50%;
  border: 1px solid #eee;
  background: #fff;
  margin-right: .2rem;
  transition: .3s all;
}
.invoiceOrderRecord .panel .all-check i {
  color: #fff;
  font-size: .6rem;
}
.invoiceOrderRecord .panel .all-check[ac=true] {
  background: #f06553;
  border-color: #f06553;
}
.invoiceOrderRecord .panel .next {
  display: inline-block;
  text-align: center;
  line-height: 2.5rem;
  width: 6rem;
  color: #999;
  background: #eee;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  border-radius: .2rem;
  font-size: 1rem;
  transition: .3s all;
}
.invoiceOrderRecord .panel .next[ac=true] {
  color: #fff;
  background: #f06553;
}
.invoiceOrderRecord .wujilv {
  height: 40vh;
  padding-top: 30vh;
}
.invoiceOrderRecord .wujilv h2 {
  text-align: center;
}



