
.PTserver .PTserverContent .describe {
  height: 10rem;
  padding-top: 1rem;
  background-color: #fff;
  position: relative;
}
.PTserver .PTserverContent .describe .describeContent {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.PTserver .PTserverContent .describe h2 {
  font-size: 2rem;
  color: #333;
  text-align: center;
  margin-top: 1rem;
}
.PTserver .PTserverContent .describe h3 {
  color: #999999;
  font-size: 1.25rem;
  text-align: center;
  margin-top: 1.4rem;
}
.PTserver .PTserverContent .orderInform {
  margin-top: 1rem;
  padding: 0 1rem;
  padding-bottom: 1rem;
  margin-bottom: 3rem;
  background-color: #fff;
}
.PTserver .PTserverContent .orderInform .row {
  line-height: 5rem;
  border-bottom: 1px solid #eee;
  padding: 0 1rem;
}
.PTserver .PTserverContent .orderInform .row .iconfont {
  margin-right: 0.1rem;
}
.PTserver .PTserverContent .orderInform .row .val {
  font-size: 1.3rem;
  color: #333333;
}
.PTserver .PTserverContent .orderInform .row:nth-last-child(1) {
  border-bottom: none;
}
.PTserver .PTserverContent .orderInform .time .iconfont {
  color: #fab10a;
}
.PTserver .PTserverContent .orderInform .startAdd,
.PTserver .PTserverContent .orderInform .endAdd {
  padding: 0 1rem;
  line-height: 2rem;
}
.PTserver .PTserverContent .orderInform .startAdd .bottom .val,
.PTserver .PTserverContent .orderInform .endAdd .bottom .val {
  font-size: 1rem;
  padding-left: 1.6rem;
  color: #999999;
}
.PTserver .PTserverContent .orderInform .startAdd {
  padding-top: 1.2rem;
  padding-bottom: 0.3rem;
  border-bottom: none;
}
.PTserver .PTserverContent .orderInform .startAdd .iconfont {
  color: #3cafb9;
}
.PTserver .PTserverContent .orderInform .endAdd {
  padding-bottom: 1.8rem;
  padding-top: 0.3rem;
}
.PTserver .PTserverContent .orderInform .endAdd .iconfont {
  color: #f06553;
}
.PTserver .PTserverContent .orderInform .more {
  border-top: 1px solid #eee;
  position: relative;
  height: auto;
  transition: 0.3s all;
  padding-bottom: 1.8rem;
  margin-top: 0.5rem;
}
.PTserver .PTserverContent .orderInform .more .moreBut {
  display: inline-block;
  position: absolute;
  left: 50%;
  top: -1.7rem;
  transform-origin: 50% 50%;
  transform: translate(-50%, 0) scaleY(1);
  width: 3.5rem;
  height: 3.5rem;
  background: url(../../../assets/img/paotui/xia.png) no-repeat center #fff;
  background-size: 2rem 2rem;
  transition: 0.3s all;
  border-radius: 50%;
  overflow: hidden;
}
.PTserver .PTserverContent .orderInform .more .moreContent {
  height: auto;
  line-height: 2.5rem;
  padding: 1rem 0;
  padding-top: 1.8rem;
  text-align: center;
}
.PTserver .PTserverContent .orderInform .more .moreContent .list h3 {
  font-size: 1.25rem;
  color: #333;
}
.PTserver .PTserverContent .orderInform .more .moreContent .list p {
  font-size: 1rem;
  line-height: 1.3rem;
  color: #999999;
}
.PTserver .PTserverContent .orderInform .more .moreContent .allSum {
  text-align: center;
}
.PTserver .PTserverContent .orderInform .money {
  position: relative;
  height: auto;
  margin-top: -1px;
  transition: 0.3s all;
}
.PTserver .PTserverContent .orderInform .money .moreContent {
  overflow: hidden;
  line-height: 1.9rem;
  height: 0;
  text-align: center;
}
.PTserver .PTserverContent .orderInform .money .moreContent .list {
  line-height: 1.666rem;
  color: #999999;
  font-size: 1rem;
}
.PTserver .PTserverContent .orderInform .money .moreContent .point {
  line-height: 2.3rem;
  font-size: 1rem;
  color: #333;
}
.PTserver .PTserverContent .orderInform .money .moreContent .point .sum {
  color: #f06553;
  font-size: 1.5rem;
  margin: 0 0.3rem;
}
.PTserver .PTserverContent .orderInform .money .moreContent .list:nth-last-child(1) {
  padding-bottom: 1.5rem;
}
.PTserver .PTserverContent .orderInform .money .moneyBut {
  display: inline-block;
  position: absolute;
  left: 50%;
  top: -1.7rem;
  transform-origin: 50% 50%;
  transform: translate(-50%, 0) scaleY(1);
  width: 3.5rem;
  height: 3.5rem;
  background: url(../../../assets/img/paotui/xia.png) no-repeat center #fff;
  background-size: 2rem 2rem;
  transition: 0.3s all;
  border-radius: 50%;
  overflow: hidden;
}
.PTserver .PTserverContent .orderInform .moneyShow .moneyBut {
  transform: translate(-50%, 0) scaleY(-1);
}
.PTserver .PTserverContent .orderInform .moneyShow .moreContent {
  height: auto;
  padding-top: 2rem;
}



