
.circuitMod {
  width: 100%;
  background-color: #fff;
  border-radius: 0.333rem;
  overflow: hidden;
  margin-bottom: 1rem;
}
.circuitMod .circuitModcontent {
  text-align: center;
  width: 100%;
}
.circuitMod .circuitModcontent .city {
  box-sizing: border-box;
  padding: 1rem;
  border-bottom: 1px solid #eee;
  width: 100%;
  position: relative;
}
.circuitMod .circuitModcontent .city .cityItem {
  width: 48%;
}
.circuitMod .circuitModcontent .city .cityItem h3 {
  color: #333;
  font-size: 1.6rem;
  margin-bottom: 0.1rem;
}
.circuitMod .circuitModcontent .city .cityItem P {
  color: #999999;
  font-size: 1rem;
}
.circuitMod .circuitModcontent .city .inverse {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.circuitMod .circuitModcontent .city .inverse i {
  font-size: 1.6rem;
  color: #666666;
}
.circuitMod .circuitModcontent .charges {
  box-sizing: border-box;
  width: 100%;
  padding: 1.8rem 1rem;
}
.circuitMod .circuitModcontent .charges .chargesItem {
  width: 32%;
}
.circuitMod .circuitModcontent .charges .chargesItem h2 {
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
  color: #666666;
}
.circuitMod .circuitModcontent .charges .chargesItem P {
  font-size: 0.833rem;
  color: #999999;
}
.circuitMod .circuitModcontent .charges .chargesItem P .sum {
  font-size: 1.3rem;
  color: #f06553;
}



