
.textarea {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border: 1px solid #d3d3d4;
  padding: 0.5rem;
  border-radius: 0.333rem;
  overflow: hidden;
}
.textarea textarea {
  border: none;
  resize: none;
  width: 100%;
  height: 100%;
}



