
.affirm {
  min-height: 100%;
}
.affirm .content {
  box-sizing: border-box;
  width: 100vw;
  background-color: #f3f4f5;
}
.affirm .content .grade {
  width: 100%;
  padding: 0 3.25rem;
  box-sizing: border-box;
  text-align: center;
}
.affirm .content .grade .evaluate {
  width: 100%;
  padding-top: 1.6rem;
}
.affirm .content .grade .evaluate .evaluateTag {
  width: 100%;
}
.affirm .content .grade .evaluate .evaluateTag .TagList {
  width: 100%;
}
.affirm .content .grade .evaluate .remark {
  box-sizing: border-box;
  margin-top: 1rem;
  height: 8.83rem;
  width: 100%;
}
.affirm .content .submit {
  box-sizing: border-box;
  width: 100%;
  padding: 0 3.25rem;
  margin-top: 1.5rem;
  padding-bottom: 1rem;
}
.affirm .content .submit span {
  width: 100%;
  height: 3.75rem;
  line-height: 3.75rem;
  background-color: #f06553;
  color: #fff;
  text-align: center;
  border-radius: 0.333rem;
  overflow: hidden;
  font-size: 1rem;
}



