
.XKTabs {
  width: 100%;
  height: 100%;
  z-index: 100;
  position: relative;
  min-height: 3.8rem;
}
.XKTabs .XKTabsContent {
  width: calc(100% - 2.8rem);
  height: inherit;
  overflow: hidden;
  overflow-x: auto;
  float: left;
  box-shadow: 0 0 10px #ffffff inset;
}
.XKTabs .XKTabsContent ul {
  width: auto;
  min-width: 100%;
}
.XKTabs .XKTabsContent li {
  float: left;
  text-align: center;
  font-size: 1.25rem;
  transition: 0.3s all;
}
.XKTabs .XKTabsContent li a {
  color: #333;
  transition: 0.3s all;
}
.XKTabs .XKTabsContent .tabAction a {
  color: #f06553;
}
.XKTabs .shade {
  position: absolute;
  height: inherit;
  z-index: 10;
  width: 2rem;
}
.XKTabs .shade img {
  height: 100%;
  width: 100%;
}
.XKTabs .shadeLeft {
  left: -0.2rem;
  top: 0;
}
.XKTabs .shadeLeft img {
  transform: rotate(180deg);
}
.XKTabs .shadeRight {
  right: 2.78rem;
  top: 0;
}
.XKTabs .gengduo {
  float: right;
  width: 2rem;
  height: 2rem;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
  transform-origin: 50% 50%;
  border-radius: 50%;
  overflow: hidden;
  z-index: 10;
}
.XKTabs .gengduo i {
  font-size: 1.25rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(0deg);
  opacity: 1;
}
.XKTabs .gengduoShow i {
  transition: 0.3s all;
  transform: translate(-50%, -50%) rotate(180deg);
}
.XKTabs .allBusiness {
  box-sizing: border-box;
  position: absolute;
  left: -3.5rem;
  top: 89%;
  background-color: #fff;
  width: 100vw;
  padding: 1rem;
  text-align: center;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.05);
  transform-origin: 0 0;
  transform: scale(1, 0);
  transition: 0.3s all;
  opacity: 0;
  z-index: 50;
}
.XKTabs .allBusiness h2 {
  display: inline-block;
  border-bottom: 1px solid #eee;
  width: 100%;
}
.XKTabs .allBusiness .Content {
  width: 100%;
  padding: 0.5rem 0;
}
.XKTabs .allBusiness .Content .row {
  line-height: 4rem;
  float: left;
  width: 25%;
}
.XKTabs .allBusiness .Content .row a {
  color: #333;
}
.XKTabs .allBusinessShow {
  transform: scale(1, 1);
  opacity: 1;
  z-index: 20;
}
.XKTabs .modal {
  position: fixed;
  left: 0;
  top: 3.7rem;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}



