
.couponMod {
  height: 7.708333rem;
  width: 100%;
  background-size: 100%;
  margin: 1rem 0;
  overflow: hidden;
}
.couponMod .top {
  border-radius: 0.333rem 0.333rem 0 0;
  background-color: #fff;
  height: 5.833rem;
  overflow: hidden;
}
.couponMod .top .left {
  position: relative;
  width: 8.1666rem;
  height: 100%;
  float: left;
}
.couponMod .top .left .content {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.couponMod .top .left .content h2 {
  color: #f06553;
  text-align: center;
}
.couponMod .top .left .content h2 span {
  font-size: 0.8333rem;
}
.couponMod .top .left .content h2 .sum {
  font-size: 2rem;
}
.couponMod .top .left .content P {
  text-align: center;
  color: #f06553;
  font-size: 0.8333rem;
}
.couponMod .top .left .line {
  height: 100%;
  width: 0.7rem;
  background: url(../../../../assets/img/coupon/yuan.png) repeat-y #f3f4f5;
  position: absolute;
  right: -0.7rem;
  top: 0;
}
.couponMod .top .right {
  float: left;
  width: calc(100% - 8.1666rem);
  height: 100%;
  box-sizing: border-box;
  position: relative;
}
.couponMod .top .right .business {
  position: absolute;
  left: 2.1rem;
  top: 25%;
  transform: translate(0, -50%);
  color: #999999;
}
.couponMod .top .right .content {
  position: absolute;
  left: 2.1rem;
  top: 50%;
  transform: translate(0, -50%);
}
.couponMod .top .right h2 {
  color: #333333;
  font-size: 1.166rem;
}
.couponMod .top .right p {
  font-size: 0.8333rem;
  color: #999999;
  margin-top: 0.5rem;
}
.couponMod .top .daijia {
  background: url(../../../../assets/img/coupon/daijia.png) no-repeat 16.5rem center;
  background-size: 4.166rem;
}
.couponMod .top .paotui {
  background: url(../../../../assets/img/coupon/paotui.png) no-repeat 16.5rem center;
  background-size: 4.166rem;
}
.couponMod .top .zhuanche {
  background: url(../../../../assets/img/coupon/zhuanche.png) no-repeat 16.5rem center;
  background-size: 4.166rem;
}
.couponMod .top .zhuanxian {
  background: url(../../../../assets/img/coupon/zhuanxian.png) no-repeat 16.5rem center;
  background-size: 4.166rem;
}
.couponMod .bottom {
  box-sizing: border-box;
  height: 2rem;
  line-height: 2rem;
  border-radius: 0 0 0.333rem 0.333rem;
  background-color: #fafafa;
  font-size: 0.8333rem;
  color: #e9b24a;
  padding-left: 1rem;
}
.couponMod .bottom span {
  color: #f06553;
}
.useCouponMod {
  color: #bbbbbb !important;
}
.useCouponMod .bottom {
  color: #bbbbbb !important;
}
.useCouponMod h2,
.useCouponMod p,
.useCouponMod span {
  color: #bbbbbb !important;
}



