
.ZC .ZCcontent {
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem;
  padding-bottom: 1rem;
}
.ZC .ZCcontent .choosedCarTypeStyle {
  color: #f06553;
}
.ZC .ZCcontent .banner {
  width: 100%;
  height: 5rem;
  border-radius: 0.333rem;
  overflow: hidden;
}
.ZC .ZCcontent .OrderInfor {
  box-sizing: border-box;
  margin-top: 1rem;
  width: 100%;
  border-radius: 0.333rem;
  background-color: #fff;
}
.ZC .ZCcontent .OrderInfor .typeNav {
  background-color: #eeeeee;
  width: 100%;
  height: 3rem;
  line-height: 3.75rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont {
  min-height: 3rem;
  width: 100%;
  background-color: #fff;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite {
  background-color: #fff;
  border-radius: 0.333rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .title {
  text-align: center;
  font-size: 1rem;
  line-height: 3rem;
  border-bottom: 1px solid #eee;
  color: #999999;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row {
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid #f2f2f2;
  line-height: 3.5rem;
  padding: 0 1rem;
  transition: 0.3s all;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row i {
  float: left;
  margin-right: 0.5rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row .icon-yuan {
  font-size: 1rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row .val {
  min-width: 90%;
  float: left;
  width: auto;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row1,
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row5,
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row6 {
  margin: 0 1rem;
  width: calc(100% - 2rem);
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row1 i,
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row5 i,
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row6 i {
  color: #3cafb9;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row2 {
  padding-left: 2rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row2 i {
  color: #f06553;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 {
  text-align: center;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top {
  width: 100%;
  padding: 0 1rem;
  height: 3.666rem;
  overflow: hidden;
  line-height: 3.666rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top .list {
  box-sizing: border-box;
  width: 49%;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top .list i {
  font-size: 0.833rem;
  color: #bbbbbb;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top .list p {
  float: left;
  font-size: 1rem;
  color: #bbbbbb;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top .list .listVal {
  float: left;
  width: 80%;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top .listAc i,
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top .listAc p {
  color: #333;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top .bottom {
  display: inline-block;
  width: 75%;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row3 .top .bottom input {
  box-sizing: border-box;
  display: inline-block;
  line-height: 2rem;
  width: 100%;
  padding: 0 0.5rem;
  background-color: #f3f4f5;
  border: none;
  border-radius: 0.333rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row4 {
  text-align: center;
  padding: 0 1rem;
  /*color: #999999;*/
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row4 .typeCar {
  text-align: center;
  font-size: 1rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row4 .typeCar .list {
  display: inline-block;
  margin: 0 0.5rem;
  color: #666;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row4 .typeCar .list b {
  color: #f06553;
  margin: 0 0.3rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row5 i {
  font-size: 1.1rem;
  color: #bbbbbb;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row5 input {
  border: none;
  font-size: 1.1666rem;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row6 i {
  font-size: 1rem;
  color: #bbbbbb;
}
.ZC .ZCcontent .OrderInfor .OrderInforCont .setSite .row6 input {
  border: none;
  font-size: 1.1666rem;
}
.ZC .ZCcontent .OrderInfor .totalNum {
  box-sizing: border-box;
  width: 100%;
  background-color: #fff;
  padding: 0 1rem;
  margin-top: 1rem;
  text-align: center;
  position: relative;
}
.ZC .ZCcontent .OrderInfor .totalNum .row {
  width: 50%;
  float: left;
  line-height: 3rem;
  font-size: 0.8333rem;
}
.ZC .ZCcontent .OrderInfor .totalNum .row .red {
  color: #f06550;
  font-size: 1.5rem;
  margin: 0 0.3rem;
}
.ZC .ZCcontent .OrderInfor .totalNum .line {
  width: 1px;
  height: 50%;
  position: absolute;
  background-color: #eee;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.ZC .ZCcontent .OrderInfor .totalNum i {
  float: none;
  color: #999;
  font-size: 0.8333rem;
}
.ZC .ZCcontent .submitOrder {
  width: 100%;
  box-sizing: border-box;
  margin-top: 1rem;
}
.ZC .ZCcontent .submitOrder span {
  width: 100%;
  color: #fff;
  font-size: 1.25rem;
  background-color: #f06553;
  text-align: center;
  border-radius: 0.333rem;
  height: 3.75rem;
  line-height: 3.75rem;
}
.ZC .ZCcontent .inputBoxContent input:-ms-input-placeholder {
  font-size: 1rem;
}
.ZC .ZCcontent .inputBoxContent input::placeholder {
  font-size: 1rem;
}
.ZC .costBox ul li {
  height: 2rem;
  line-height: 2rem;
  color: #999999;
}
.ZC .costBox ul li .type {
  text-align: center;
  width: 100%;
  height: inherit;
  position: relative;
}
.ZC .costBox ul li .type .name {
  height: 1.8rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 0 0.5rem;
  display: inline-block;
  border-radius: 1rem;
  background: #f06553;
  color: #fff;
  font-size: 1rem;
}
.ZC .costBox ul li .name {
  float: left;
}
.ZC .costBox ul li .time,
.ZC .costBox ul li .val {
  float: right;
}
.ZC .costBox ul li .time {
  margin-right: 0.8rem;
  color: #666;
}
.ZC .costBox ul li .sum {
  color: #f06553;
}
.ZC .costBox ul li .unit {
  margin-left: 0.3rem;
}
.ZC .costBox ul .carName {
  margin-bottom: 0.8rem;
  margin-top: 0.8rem;
}
.ZC .costBox .BoxContent {
  max-height: 20rem;
  overflow: scroll !important;
}
.ZC .costBox .introduce {
  border-top: 1px solid #eee;
  margin-top: 0.5rem;
  padding-top: 1.2rem;
  line-height: 1.8rem;
  color: #666;
}



