
.PTawait {
  width: 100%;
}
.PTawait .PTawaitContent {
  width: 100%;
}
.PTawait .PTawaitContent .loads {
  text-align: center;
  background-color: #fff;
  padding-bottom: 3rem;
}
.PTawait .PTawaitContent .loads .Type {
  margin-top: 3.75rem;
  height: 1.8rem;
  line-height: 1.9rem;
  padding: 0 0.8333rem;
  background-color: #f06553;
  color: #fff;
  border-radius: 0.9rem;
  font-size: 0.8333rem;
}
.PTawait .PTawaitContent .loads .animation {
  margin: 3rem 0;
}
.PTawait .PTawaitContent .loads .animation .animationContent {
  width: 5.7rem;
  height: 5.7rem;
  position: relative;
}
.PTawait .PTawaitContent .loads .animation .animationContent img {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
  transform-origin: 50% 50%;
}
.PTawait .PTawaitContent .loads .animation .animationContent p {
  position: absolute;
  width: 5rem;
  font-size: 0.8333rem;
  color: #999999;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.PTawait .PTawaitContent .loads h2 {
  font-size: 1.25rem;
  color: #333333;
  margin-bottom: 1rem;
}
.PTawait .PTawaitContent .loads .cancelOrder {
  color: #f06553;
  font-size: 0.833rem;
}
.PTawait .PTawaitContent .orderInform {
  margin-top: 1rem;
  padding: 0 1rem;
  padding-bottom: 3rem;
  background-color: #fff;
}
.PTawait .PTawaitContent .orderInform .row {
  line-height: 5rem;
  border-bottom: 1px solid #eee;
  padding: 0 1rem;
}
.PTawait .PTawaitContent .orderInform .row .iconfont {
  margin-right: 0.1rem;
}
.PTawait .PTawaitContent .orderInform .row .val {
  font-size: 1.3rem;
  color: #333333;
}
.PTawait .PTawaitContent .orderInform .time .iconfont {
  color: #fab10a;
}
.PTawait .PTawaitContent .orderInform .startAdd,
.PTawait .PTawaitContent .orderInform .endAdd {
  padding: 0 1rem;
  line-height: 2rem;
}
.PTawait .PTawaitContent .orderInform .startAdd .bottom .val,
.PTawait .PTawaitContent .orderInform .endAdd .bottom .val {
  font-size: 1rem;
  padding-left: 1.6rem;
  color: #999999;
}
.PTawait .PTawaitContent .orderInform .startAdd {
  padding-top: 1.2rem;
  padding-bottom: 0.3rem;
  border-bottom: none;
}
.PTawait .PTawaitContent .orderInform .startAdd .iconfont {
  color: #3cafb9;
}
.PTawait .PTawaitContent .orderInform .endAdd {
  padding-bottom: 1.8rem;
  padding-top: 0.3rem;
  border: none;
}
.PTawait .PTawaitContent .orderInform .endAdd .iconfont {
  color: #f06553;
}
.PTawait .PTawaitContent .orderInform .more {
  position: relative;
  height: auto;
  margin-top: 0.5rem;
  transition: 0.3s all;
  border-top: 1px solid #eee;
  border-bottom: none;
}
.PTawait .PTawaitContent .orderInform .more .moreBut {
  display: inline-block;
  position: absolute;
  left: 50%;
  top: -1.7rem;
  transform-origin: 50% 50%;
  transform: translate(-50%, 0) scaleY(1);
  width: 3.5rem;
  height: 3.5rem;
  background: url(../../../assets/img/paotui/xia.png) no-repeat center #fff;
  background-size: 2rem 2rem;
  transition: 0.3s all;
  border-radius: 50%;
  overflow: hidden;
}
.PTawait .PTawaitContent .orderInform .more .moreContent {
  height: 0;
  overflow: hidden;
}
.PTawait .PTawaitContent .orderInform .moreShow .moreBut {
  transform: translate(-50%, 0) scaleY(-1);
}
.PTawait .PTawaitContent .orderInform .moreShow .moreContent {
  height: auto;
  line-height: 2.5rem;
  padding: 1rem 0;
  padding-top: 1.8rem;
  text-align: center;
}
.PTawait .PTawaitContent .orderInform .moreShow .moreContent .list h3 {
  font-size: 1.25rem;
  color: #333;
}
.PTawait .PTawaitContent .orderInform .moreShow .moreContent .list p {
  font-size: 1rem;
  line-height: 1.3rem;
  color: #999999;
}
.PTawait .DjCancelOrder {
  border: 1px solid #eee;
  height: 10rem;
}
.PTawait .DjCancelOrder textarea {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: none;
  resize: none;
  font-size: 1rem;
  padding: 0.5rem;
}



