
.DaiJia .content {
  box-sizing: border-box;
  width: 100%;
  padding: 0 1rem;
}
.DaiJia .content .banner {
  width: 100%;
  height: 5rem;
  border-radius: 0.333rem;
  overflow: hidden;
}
.DaiJia .content .typeNav {
  background-color: #eeeeee;
  width: 100%;
  height: 3rem;
  line-height: 3.75rem;
}
.DaiJia .content .setSite {
  background-color: #fff;
  margin-top: 1rem;
  border-radius: 0.333rem;
}
.DaiJia .content .setSite .title {
  text-align: center;
  font-size: 1rem;
  line-height: 3rem;
  border-bottom: 1px solid #eee;
  color: #999999;
}
.DaiJia .content .setSite .row {
  width: 100%;
  box-sizing: border-box;
  border-bottom: 1px solid #f2f2f2;
  line-height: 3.5rem;
  padding: 0 1rem;
  transition: 0.3s all;
}
.DaiJia .content .setSite .row i {
  float: left;
  margin-right: 0.5rem;
}
.DaiJia .content .setSite .row .icon-yuan {
  font-size: 1rem;
}
.DaiJia .content .setSite .row .val {
  min-width: 90%;
  float: left;
  width: auto;
}
.DaiJia .content .setSite .row1 {
  margin: 0 1rem;
  width: calc(100% - 2rem);
}
.DaiJia .content .setSite .row1 i {
  color: #3cafb9;
}
.DaiJia .content .setSite .row2 {
  padding-left: 2rem;
}
.DaiJia .content .setSite .row2 i {
  color: #f06553;
}
.DaiJia .content .setSite .row3 {
  text-align: center;
}
.DaiJia .content .setSite .row3 .top {
  width: 100%;
  /*padding: 0 1rem;*/
  height: 3.666rem;
  overflow: hidden;
  line-height: 3.666rem;
}
.DaiJia .content .setSite .row3 .top .list {
  box-sizing: border-box;
  width: 49%;
}
.DaiJia .content .setSite .row3 .top .list i {
  font-size: 0.833rem;
  color: #bbbbbb;
}
.DaiJia .content .setSite .row3 .top .list p {
  float: left;
  font-size: 1rem;
  color: #bbbbbb;
}
.DaiJia .content .setSite .row3 .top .list .listVal {
  float: left;
  /*width: 80%;*/
}
.DaiJia .content .setSite .row3 .top .listAc i,
.DaiJia .content .setSite .row3 .top .listAc p {
  color: #333;
}
.DaiJia .content .setSite .row3 .top .bottom {
  display: inline-block;
  width: 75%;
}
.DaiJia .content .setSite .row3 .top .bottom input {
  box-sizing: border-box;
  display: inline-block;
  line-height: 2rem;
  width: 100%;
  padding: 0 0.5rem;
  background-color: #f3f4f5;
  border: none;
  border-radius: 0.333rem;
}
.DaiJia .content .setSite .totalNum {
  position: relative;
}
.DaiJia .content .setSite .totalNum .line {
  width: 1px;
  height: 50%;
  background-color: #eee;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.DaiJia .content .setSite .totalNum .row {
  box-sizing: border-box;
  float: left;
  width: 50%;
  text-align: center;
  border-bottom: none;
  font-size: 0.8333rem;
}
.DaiJia .content .setSite .totalNum .row .couponSelectList .val {
  float: right;
  width: auto;
}
.DaiJia .content .setSite .totalNum .red {
  font-size: 1.5rem;
  color: #f06553;
  margin: 0 0.3rem;
}
.DaiJia .content .setSite .totalNum i {
  float: none;
  color: #999;
  font-size: 0.8333rem;
}
.DaiJia .content .callOutBut {
  box-sizing: border-box;
  margin-top: 1rem;
  width: 100%;
}
.DaiJia .content .callOutBut button {
  width: 100%;
  text-align: center;
  height: 3.75rem;
  line-height: 3.75rem;
  border-radius: 0.333rem;
  background-color: #f06553;
  color: #fff;
  font-size: 1.25rem;
  border: none;
}
.DaiJia .content .callOutBut .disable {
  background-color: #ccc;
}
.DaiJia .content .xieyi {
  margin-top: 0.5rem;
}
.DaiJia .content .xieyi p {
  font-size: 1rem;
  color: #999999;
}
.DaiJia .content .xieyi p a {
  color: #f06553;
}
.DaiJia .costBox ul li {
  height: 2rem;
  line-height: 2rem;
  color: #999999;
}
.DaiJia .costBox ul li .name {
  float: left;
}
.DaiJia .costBox ul li .time,
.DaiJia .costBox ul li .val {
  float: right;
}
.DaiJia .costBox ul li .time {
  margin-right: 0.8rem;
  color: #666;
}
.DaiJia .costBox ul li .sum {
  color: #f06553;
}
.DaiJia .costBox ul li .unit {
  margin-left: 0.3rem;
}
.DaiJia .costBox .introduce {
  border-top: 1px solid #eee;
  margin-top: 0.5rem;
  padding-top: 1.2rem;
  line-height: 1.8rem;
  color: #666;
}



