
.Settlement .Settlementcont {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  background-color: #f3f4f5;
  padding: 1rem;
}
.Settlement .title {
  height: 1.5rem;
  line-height: 1.5rem;
  text-align: center;
  position: relative;
}
.Settlement .title h2 {
  display: inline-block;
  color: #333;
  font-size: 1.6rem;
  padding: 0 0.8rem;
  background-color: #fff;
  z-index: 10;
  position: relative;
}
.Settlement .title .line {
  width: 100%;
  height: 1px;
  background-color: #eee;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.Settlement .feiyong {
  background-color: #fff;
  padding: 1rem;
  padding-top: 1.5rem;
  padding-bottom: 1rem;
}
.Settlement .feiyong .content {
  text-align: center;
  padding-top: 1.8rem;
}
.Settlement .feiyong .content .sum {
  color: #f06553;
}
.Settlement .feiyong .content .sum .xiaoye,
.Settlement .feiyong .content .sum .danwei {
  font-size: 1.5rem;
}
.Settlement .feiyong .content .sum .daxie {
  font-size: 3rem;
}
.Settlement .detailed {
  background-color: #fff;
  text-align: center;
  line-height: 1.5rem;
  height: 1.5rem;
  color: #999999;
  padding: 0.5rem 0 1.8rem 0;
  font-size: 1rem;
}
.Settlement .detailed span {
  display: inline-block;
}
.Settlement .detailed span i {
  font-size: 1.3rem;
  padding-left: 0.3rem;
  color: #999999;
}
.Settlement .payType {
  background-color: #fff;
  margin-top: 1rem;
  padding: 1rem;
  padding-top: 1.5rem;
}
.Settlement .payType .payTypeContent {
  padding-top: 1rem;
}
.Settlement .payType .payTypeContent ul {
  width: 100%;
}
.Settlement .payType .payTypeContent ul li {
  box-sizing: border-box;
  line-height: 3.6666rem;
  height: 3.6666rem;
  font-size: 1.3rem;
  color: #434343;
  width: 100%;
  position: relative;
  padding: 0 1rem;
}
.Settlement .payType .payTypeContent ul li > span {
  width: 100%;
}
.Settlement .payType .payTypeContent ul li i {
  font-size: 1.5rem;
}
.Settlement .payType .payTypeContent ul li .Icon {
  margin-right: 0.5rem;
}
.Settlement .payType .payTypeContent ul li .select {
  display: inline-block;
  width: 1.8rem;
  height: 1.8rem;
  box-sizing: border-box;
  overflow: hidden;
  border-radius: 50%;
  border: 1px solid #cccccc;
  position: absolute;
  right: 1.1666rem;
  top: 50%;
  transform: translate(0, -50%);
  transition: 0.3s all;
}
.Settlement .payType .payTypeContent ul li .select i {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 0.3rem;
  opacity: 0;
  color: #fff;
  transition: 0.3s all;
}
.Settlement .payType .payTypeContent ul li .selectAction {
  background-color: #f06553;
  border-color: #f06553;
}
.Settlement .payType .payTypeContent ul li .selectAction i {
  opacity: 1;
}
.Settlement .payType .payTypeContent ul .icon-yue {
  color: #fe5459;
}
.Settlement .payType .payTypeContent ul .icon-weixin {
  color: #00c801;
}
.Settlement .payType .payTypeContent ul .icon-qiandan-copy {
  color: #ffb400;
}
.Settlement .submitBut {
  box-sizing: border-box;
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.Settlement .submitBut span {
  width: 100%;
  line-height: 3.666rem;
  height: 3.666rem;
  border-radius: 0.333rem;
  background-color: #f06553;
  text-align: center;
  color: #fff;
  font-size: 1.4rem;
}
.Settlement .costBox ul li {
  height: 2rem;
  line-height: 2rem;
  color: #999999;
}
.Settlement .costBox ul li .name {
  float: left;
}
.Settlement .costBox ul li .time,
.Settlement .costBox ul li .val {
  float: right;
}
.Settlement .costBox ul li .time {
  margin-right: 0.8rem;
  color: #333333;
}
.Settlement .costBox ul li .sum {
  color: #f06553;
}
.Settlement .costBox ul li .unit {
  margin-left: 0.3rem;
}



