
.inform {
  background: #f3f4f5;
  height: 100vh;
  width: 100vw;
}
.inform .informContent {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
.inform .informContent ul {
  margin-bottom: 1rem;
}
.inform .informContent .wujilv {
  height: 40vh;
  padding-top: 30vh;
}
.inform .informContent .wujilv h2 {
  text-align: center;
}
.inform .loadDataBut {
  box-sizing: border-box;
  width: 100%;
  line-height: 3.5rem;
  padding: 0 1rem;
  margin-bottom: 1rem;
}
.inform .loadDataBut span {
  width: 100%;
  background-color: #fff;
  border-radius: 0.333rem;
  text-align: center;
}



