
.friendOrder {
  text-align: center;
  padding-top: 4rem;
}
.friendOrder .content h2 {
  color: #333;
  font-size: 1.5rem;
}
.friendOrder .content h2 span {
  color: #f06553;
  font-size: 3rem;
}
.friendOrder .content .recordOrder {
  box-sizing: border-box;
  margin-top: 1.5rem;
  width: 100%;
  padding: 0 1rem;
}
.friendOrder .content .recordOrder table {
  width: 100%;
  border-collapse: collapse;
  /* 边框合并属性  */
}
.friendOrder .content .recordOrder table td,
.friendOrder .content .recordOrder table th {
  box-sizing: border-box;
  width: 25%;
  padding: 0 0.6rem;
  height: 2.5rem;
  line-height: 2.5rem;
  text-align: center;
}
.friendOrder .content .recordOrder .Title {
  overflow: hidden;
}
.friendOrder .content .recordOrder .Title th {
  background: #ffd8d6;
  color: #666666;
}
.friendOrder .content .recordOrder .Tbody {
  border: 1px solid #ffd8d6;
  max-height: 25rem;
  overflow: hidden;
  overflow-y: scroll;
}
.friendOrder .content .recordOrder .Tbody td {
  color: #333333;
}
.friendOrder .content .recordOrder .Tbody .rebate {
  color: #f06553;
}



