
.navTabs {
  width: 100%;
  height: inherit;
  background: #fff;
}
.navTabs .Content {
  width: 100%;
  overflow-x: scroll;
  height: inherit;
}
.navTabs .Content ul {
  min-width: 100%;
  height: inherit;
}
.navTabs .Content ul li {
  position: relative;
  height: inherit;
  float: left;
  text-align: center;
}
.navTabs .Content ul li .val {
  display: inline-block;
  text-align: center;
  padding: 0 1rem;
}
.navTabs .Content ul li:after {
  content: '';
  width: 1px;
  background: #eee;
  height: 50%;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0, -50%);
}
.navTabs .Content ul li:nth-last-child(1):after {
  content: '';
  display: none;
}
.navTabs .Content ul .navTabsLiAc {
  color: #f06553;
}



