
#datePicker {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  /*.backColor {*/
  /*width: 100%;*/
  /*height: 100%;*/
  /*background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2), rgba(0, 0, 0, 0.1));*/
  /*}*/
}
#datePicker .mode {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
#datePicker .lines {
  width: 100%;
  height: 30px;
  border-top: 1px solid #efefef;
  border-bottom: 1px solid #efefef;
  position: absolute;
  top: 8.2rem;
  left: 0;
}
#datePicker .timePick {
  width: 100%;
  /*padding-bottom: 20px;*/
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #fff;
  height: 200px;
}
#datePicker .timePick .bottons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  color: #000;
  font-size: 1.2rem;
  height: 2rem;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.5rem 0;
  /*margin-top: 1rem;*/
}
#datePicker .timePick .bottons span {
  display: inline-block;
}
#datePicker .timePick .bottons span:first-child {
  color: #999999;
}
#datePicker .timePick .bottons span:last-child {
  color: #f06553;
}
#datePicker .timePick .picker {
  display: block;
  /*width: 100%;*/
  /*height: 90%;*/
  position: absolute;
  bottom: 0;
  margin-top: 35px;
}
#datePicker .timePick .picker .picker-items {
  display: -ms-flexbox;
  display: flex;
  font-size: 1.4rem;
  color: #999999;
}
#datePicker .timePick .picker .picker-items .picker-selected {
  color: #000000;
}



