
.unused {
  box-sizing: border-box;
  padding: 0 1rem;
  height: 100%;
  width: 100%;
  overflow-y: auto;
}
.unused .unusedContent {
  padding-bottom: 4rem;
}
.unused .unusedContentFixe {
  margin-bottom: 1rem;
}
.unused .wuyouhuiquan {
  padding-top: 30vh;
  min-height: 40vh;
}
.unused .wuyouhuiquan h2 {
  text-align: center;
}
.unused .addCoupon {
  width: 100%;
  box-sizing: border-box;
  margin-top: -1rem;
  text-align: center;
  background-color: #f3f4f5;
  padding-top: 1.5rem;
  margin-bottom: 1rem;
}
.unused .addCoupon span {
  width: 100%;
  height: 3rem;
  line-height: 2.8333rem;
  font-size: 1.25rem;
  border-radius: 0.333rem;
  background: #f06553;
  color: #fff;
}
.unused .fixe {
  position: fixed;
  width: 100%;
  padding-bottom: 1rem;
  padding-top: 0;
  bottom: 0;
  left: 0;
}
.unused .fixe span {
  width: calc(100% - 2rem);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
.unused .inputBoxContent input {
  box-sizing: border-box;
  width: 100%;
  line-height: 2.5rem;
  border: none;
  padding: 0 0.5rem;
  border-bottom: 1px solid #eee;
  font-size: 1.25rem;
  margin: 0.5rem 0;
}



