
.recordPaoTui .loadDataBut {
  box-sizing: border-box;
  width: 100%;
  line-height: 3.5rem;
  padding: 0 1rem;
}
.recordPaoTui .loadDataBut span {
  width: 100%;
  background-color: #fff;
  border-radius: 0.333rem;
  text-align: center;
}
.recordPaoTui .NullOrder {
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}
.recordPaoTui .NullOrder .img {
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
  text-align: center;
  width: 50%;
}
.recordPaoTui .NullOrder .img img {
  width: 100%;
}



